@mixin typographyV2 {
    :root {
        font-size: 62.5%;
    }

    html {
        -webkit-font-smoothing: antialiased;
        -ms-text-size-adjust: 100%; /* 2 */
        -webkit-text-size-adjust: 100%; /* 2 */
    }

    body {
        font-family: $font-Regular;
        font-size: $size-regular;
    }
    /**
    * Address `font-family` inconsistency between `textarea` and other form
    * elements.
    */

    html,
    button,
    input,
    select,
    textarea {
        font-family: inherit;
    }

    input, select, textarea {
        @include gt-font-weight;
    }

    p {
        @include gt-body-text;
        margin: 0 0 $margin-default*0.5 0;
    }

    .gt-wrapper {
        @extend %custom-paragraph;
    }

    li {
        font-size: $size-regular;
        line-height: 150%;
    }

    a {
        .sectionTag {
            color: inherit;
        }
    }

    a {
        text-decoration: none;
        color: $color-core-purple;

        @include hover {
            color: $color-dark-coral;
            @include link-underline;
        }

        @include focus-outline;
    }
    /**
    * Improve readability when focused and also mouse hovered in all browsers.
    */

    .link-icon {
        display: flex;
        margin-bottom: 6px;

        .icon {
            font-size: $size-x-large;
            line-height: 2.4rem;
            vertical-align: middle;
            margin-right: 10px;
            color: $color-core-purple;
            @include map-links-icon();
        }

        a {
            font-size: $size-regular;
        }
    }
    /**
    * Address margins set differently in IE 6/7.
    */

    p,
    pre {
        margin: 1em 0;
    }

    h1, h2, h3, h4 {
        margin: 0;
    }

    .line-length {
        max-width: $line-length;
    }

    .gt-hero-title {
        @include gt-hero-title;

        &.heavy {
            @include gt-font-weight("black", "gt-hero-title");
        }
    }

    .gt-main-title {
        @include gt-main-title;

        &.heavy {
            @include gt-font-weight("black", "gt-main-title");
        }
    }

    .gt-title {
        @include gt-title;

        &.heavy {
            @include gt-font-weight("bold", "gt-title");
        }
    }

    .gt-subtitle {
        @include gt-subtitle;

        &.heavy {
            @include gt-font-weight("", "gt-subtitle");
        }

        &.bold {
            @include gt-font-weight("bold", "gt-subtitle");
        }
    }

    .gt-small-title {
        @include gt-small-title;

        &.heavy {
            @include gt-font-weight("", "gt-small-title");
        }
        &.semi-bold {
            @include gt-font-weight("semibold", "gt-small-title");
        }
        &.bold {
            @include gt-font-weight("bold", "gt-small-title");
        }
    }

    .gt-body-text {
        @include gt-body-text;

        &.small {
            @include gt-body-text("small");
        }

        &.big {
            @include gt-body-text("big");
        }

        &.overline {
            text-transform: uppercase;
            letter-spacing: $letter-spacing;
        }

        &.heavy {
            @include gt-font-weight("bold", "gt-body-text");
        }

        &.grey {
            color: $color-rolling;
        }

        &.x-tiny {
            font-size: $size-x-tiny;
            line-height: $size-regular;
        }
    }

    .gt-category {
        font-size: var(--gt-category--font-size, $size-tiny);
        line-height: var(--gt-category--line-height, 2.2rem);
        color: var(--gt-category--color, #{$color-white});
        background: var(--gt-category--bg-color);
        display: block;
        width: fit-content;
        margin-bottom: $margin-default*0.5;
        font-weight: normal;
        border: 0.1rem solid transparent;

        &.outline,
        &.white,
        &.light,
        &.dark, &.purple {
            padding: 2px $padding-default*0.5;
            @include border-radius(2);
        }

        &.dark {
            --gt-category--bg-color: #{$color-dark-purple};
        }

        &.light {
            --gt-category--bg-color: #{$color-light-purple};
            --gt-category--color: #{$color-dark-purple};
        }

        &.purple {
            --gt-category--bg-color: #{$color-core-purple};
        }

        &.yellow {
            --gt-category--bg-color: #{$color-yellow};
            --gt-category--color: #{$color-black};
        }

        &.teal {
            --gt-category--bg-color: #{$color-teal};
            --gt-category--color: #{$color-black};
        }

        &.grey {
            --gt-category--color: #{$color-rolling};
            letter-spacing: $letter-spacing;
        }

        &.dark-grey {
            --gt-category--bg-color: #{$color-dark-grey};
            --gt-category--color: #{$color-dark-purple};
        }

        &.white {
            --gt-category--bg-color: #{$color-light-grey};
            --gt-category--color: #{$color-dark-purple};
        }

        &.trim {
            padding: 0;
        }

        &.overline {
            text-transform: uppercase;
            letter-spacing: $letter-spacing;
        }
        &.outline {
            background-color: transparent;
            border-color: var(--gt-category--bg-color, #{$color-white});
            --gt-category--color: #{$color-dark-theme};
        }
    }

    .svg-icon {
        svg {
            width: 100%;
            height: 100%;
        }
    }

    .social-icons__vi--wrapper {
        --social-icon-color: #{$color-dark-purple};
        --social-icon-hover-color: #{$color-white};
        --social-icon-bg: #{$color-light-purple};
        --social-icon-hover-bg: #{$color-dark-purple};
        --social-icon-border-radius: 100%;
        --social-icon-border-width: 0;
        --social-icon-border-color: transparent;
        --social-icon-svg-width: 1.7rem;
        --social-icon-svg-height: 1.7rem;
        --social-icon-width: #{$base-size};
        --social-icon-height: #{$base-size};
        display: flex;
        align-items: center;
        flex-flow: row nowrap;
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            margin-left: $margin-default/2;

            &:first-child {
                margin-left: 0;
            }

            a {
                @extend .social-icon__vi;
            }
        }

        &.theme-purple {
            --social-icon-color: #{$color-white};
            --social-icon-hover-color: #{$color-white};
            --social-icon-bg: #{$color-core-purple};
            --social-icon-hover-bg: #{$color-dark-purple};
        }

        &.theme-dark__purple {
            --social-icon-color: #{$color-white};
            --social-icon-hover-color: #{$color-white};
            --social-icon-bg: #{$color-dark-purple};
            --social-icon-hover-bg: #{$color-core-purple};
        }

        &.theme-light__grey {
            --social-icon-color: #{$color-dark-purple};
            --social-icon-hover-color: #{$color-dark-purple};
            --social-icon-bg: #{$color-light-grey};
            --social-icon-hover-bg: #{$color-dark-grey};
        }

        &.theme-dark__grey {
            --social-icon-color: #{$color-dark-purple};
            --social-icon-hover-color: #{$color-dark-purple};
            --social-icon-bg: #{$color-dark-grey};
            --social-icon-hover-bg: #{$color-light-grey};
        }
        // TODO: need to check with designer.
        &.theme-transparent {
            --social-icon-color: #{$color-dark-purple};
            --social-icon-hover-color: #{$color-dark-coral};
            --social-icon-bg: transparent;
            --social-icon-hover-bg: transparent;
            --social-icon-border-radius: 0;
            --social-icon-svg-width: 2rem;
            --social-icon-svg-height: 2rem;
            --social-icon-width: auto;
            --social-icon-height: auto;
        }

        &.white-text {
            --social-icon-color: #{$color-white};
        }

        &.round-border {
            --social-icon-border-width: 0.1rem;
            --social-icon-border-color: #{$color-core-purple};
            --social-icon-bg: transparent;
            --social-icon-color: #{$color-core-purple};
            --social-icon-hover-color: #{$color-white};
            --social-icon-hover-bg: #{$color-core-purple};
        }

        &.center {
            justify-content: center;
        }
    }

    .single-social__icon {
        --social-icon-color: #{$color-dark-purple};
        --social-icon-hover-color: #{$color-white};
        --social-icon-bg: #{$color-light-purple};
        --social-icon-hover-bg: #{$color-dark-purple};
        --social-icon-border-radius: 100%;
        --social-icon-border-width: 0;
        --social-icon-border-color: transparent;
        --social-icon-svg-width: 1.7rem;
        --social-icon-svg-height: 1.7rem;
        --social-icon-width: #{$base-size};
        --social-icon-height: #{$base-size};

        .social-icon__vi {
            @include transition;

            &.theme-purple {
                --social-icon-color: #{$color-white};
                --social-icon-hover-color: #{$color-white};
                --social-icon-bg: #{$color-core-purple};
                --social-icon-hover-bg: #{$color-dark-purple};
            }

            &.theme-dark__purple {
                --social-icon-color: #{$color-white};
                --social-icon-hover-color: #{$color-white};
                --social-icon-bg: #{$color-dark-purple};
                --social-icon-hover-bg: #{$color-core-purple};
            }

            &.theme-white {
                --social-icon-color: #{$color-dark-purple};
                --social-icon-hover-color: #{$color-dark-purple};
                --social-icon-bg: #{$color-white};
                --social-icon-hover-bg: #{$color-light-purple};
            }

            &.theme-light__grey {
                --social-icon-color: #{$color-dark-purple};
                --social-icon-hover-color: #{$color-dark-purple};
                --social-icon-bg: #{$color-light-grey};
                --social-icon-hover-bg: #{$color-dark-grey};
            }

            &.theme-dark__grey {
                --social-icon-color: #{$color-dark-purple};
                --social-icon-hover-color: #{$color-dark-purple};
                --social-icon-bg: #{$color-dark-grey};
                --social-icon-hover-bg: #{$color-mid-grey};
            }
            // TODO: need to check with designer.
            &.theme-transparent {
                --social-icon-color: #{$color-dark-purple};
                --social-icon-hover-color: #{$color-dark-coral};
                --social-icon-bg: transparent;
                --social-icon-hover-bg: transparent;
                --social-icon-border-radius: 0;
                --social-icon-svg-width: 2rem;
                --social-icon-svg-height: 2rem;
                --social-icon-width: auto;
                --social-icon-height: auto;
                line-height: normal;
            }

            &.white-text {
                --social-icon-color: #{$color-white};
            }

            &.purple-text {
                --social-icon-color: #{$color-core-purple};
                --social-icon-hover-color: #{$color-dark-coral};
            }

            &.red-text {
                --social-icon-color: #{$color-dark-coral};
                --social-icon-hover-color: #{$color-white};
            }

            &.grey-text {
                --social-icon-color: #{$color-rolling};
                --social-icon-hover-color: #{$color-core-purple};
            }

            &.round-border {
                --social-icon-border-width: 0.1rem;
                --social-icon-border-color: #{$color-core-purple};
                --social-icon-bg: transparent;
                --social-icon-color: #{$color-core-purple};
                --social-icon-hover-color: #{$color-white};
                --social-icon-hover-bg: #{$color-core-purple};
            }
        }
    }

    .has-hover.single-social__icon,
    [role=button].single-social__icon,
    button.single-social__icon,
    a.single-social__icon {
        @include transition;

        @include hover {
            .social-icon__vi {
                &.purple-text {
                    --social-icon-color: #{$color-dark-coral};
                    --social-icon-hover-color: #{$color-dark-coral};
                }

                &.red-text {
                    --social-icon-color: #{$color-white};
                    --social-icon-hover-color: #{$color-white};
                }

                &.grey-text {
                    --social-icon-color: #{$color-core-purple};
                    --social-icon-hover-color: #{$color-core-purple};
                }

                background: var(--social-icon-hover-bg, $color-core-purple);
                color: var(--social-icon-hover-color, $color-dark-purple);
            }
        }
    }

    .social-icon__vi {
        width: var(--social-icon-width, $base-size);
        height: var(--social-icon-height, $base-size);
        border-radius: var(--social-icon-border-radius, 100%);
        background: var(--social-icon-bg, $color-core-purple);
        color: var(--social-icon-color, $color-dark-purple);
        display: flex;
        align-items: center;
        justify-content: center;
        border: var(--social-icon-border-width, 0) solid var(--social-icon-border-color, transparent);

        svg {
            width: var(--social-icon-svg-width, 1.7rem);
            height: var(--social-icon-svg-height, 1.7rem);
            pointer-events: none;
        }
    }

    [role=button].social-icon__vi,
    .has-hover.social-icon__vi,
    button.social-icon__vi,
    a.social-icon__vi {
        @include transition;

        @include hover {
            background: var(--social-icon-hover-bg, $color-core-purple);
            color: var(--social-icon-hover-color, $color-dark-purple);
        }
    }

    .auto-soft-hyphens {
        @include soft-hyphens;
    }
}
