//display flex
@mixin flex() {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
//display flex columns
@mixin flex-columns($columns: 1) {
  -webkit-column-count: $columns; /* Chrome, Safari, Opera */
  -moz-column-count: $columns; /* Firefox */
  columns: $columns;
}
//display flex-line
@mixin flex-line($line: single) {
  -webkit-box-lines: $line;
  -moz-box-lines: $line;
  box-lines: $line;
}

//display flex-wrap
@mixin flex-wrap($wrap: nowrap) {
  -webkit-flex-wrap: $wrap;
  -moz-flex-wrap: $wrap;
  -ms-flex-wrap: $wrap;
  flex-wrap: $wrap;
}

//display flex-direction
@mixin flex-direction($direction: column) {
  -webkit-flex-direction: $direction;
  -moz-flex-direction: $direction;
  flex-direction: $direction;
  -ms-flex-direction: $direction;
}

//display flex-flow
@mixin flex-flow($flow: row) {
  -webkit-flex-flow: $flow;
  -moz-flex-flow: $flow;
  flex-flow: $flow;
  -ms-flex-flow: $flow;
}
