@import "./typographyV2";

@mixin retina {
    @media (-webkit-min-device-pixel-ratio: 1.5), ( -moz-min-device-pixel-ratio: 1.5), ( min-device-pixel-ratio: 1.5) {

        @content;
    }
}

// Specify font size in px and also in rems for browsers that support it
// Base font size on html should be set to 62.5% (10px)
@mixin font-size($sizeValue) {
    font-size: ($sizeValue / 10) + rem;
}

@mixin line-height($sizeValue) {
    line-height: ($sizeValue / 10) + rem;
}

@mixin rtl {
    [dir="rtl"] & {
        @content;
    }
}

@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }

    &::-moz-placeholder {
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }

    &::placeholder {
        @content;
    }
}

@mixin webkit-scrollbar($size, $foreground-color, $background-color: mix($foreground-color, white,  50%), $show-border: true) {
    &::-webkit-scrollbar {
        width: $size;
        height: $size;
        background: $background-color;
    }

    &::-webkit-scrollbar-thumb {
        @if $show-border {
            border: 3px solid rgba($color-black, 0);
        }
        background-clip: padding-box;
        border-radius: 100px;
        background-color: $foreground-color;
    }
}

@mixin webkit-scrollbar-thin($size, $border-color: $color-light-purple, $border-size: 2px, $position: "left") {
    &::-webkit-scrollbar {
        width: $size;
        height: $size;
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        border-#{$position}: $border-size solid $border-color;
    }
}

@mixin form-select($select-font-size: $size-regular) {
    position: relative;
    display: inline-block;
    //margin-top: -0.75 * $margin-default;
    max-width: 100%;

    select {
        width: initial;
        max-width: 100%;
        color: $color-core-purple;
        background: $color-white;
        -webkit-appearance: none;
        -moz-appearance: none;
        font-size: $select-font-size;
        padding: 1rem 4rem 1rem 2rem;
        border: 0.2rem solid $color-core-purple;
        border-radius: 0;
        line-height: 2.8rem;
        outline: none;

        &:focus-visible, &:focus {
            border-color: var(--color-focus-visible, $color-focus-visible);
        }
    }

    &:after {
        content: '';
        display: inline-block;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 7px solid $color-core-purple;
        pointer-events: none;
        position: absolute;
        right: 2rem;
        top: 50%;
        transform: translate(0, -30%);
    }

    &--light {
        select {
            color: $color-white;
            border-color: transparent;
            background: none;

            option {
                color: $color-black;
                background: $color-white;
            }
        }

        &:after {
            border-top-color: $color-white;
        }
    }

    @include media-only-mobile2 {
        width: 100%;
        max-width: 100%;

        select {
            max-width: inherit;
            width: inherit;
        }
    }
}

@mixin file-upload ($color: $color-white, $hover: $color-core-purple, $color-label: $color-white) {
    .Form {
        &FileUpload {
            &__List {
                .file-item {
                    color: $color-label;
                }
            }
        }
    }
}

@mixin setfont($font: 2.4rem, $height: 2.9rem, $weight: 300, $important: false) {
    @if($important) {
        font-size: $font !important;
        line-height: $height !important;
        font-weight: $weight !important;
    }
    @else {
        font-size: $font;
        line-height: $height;
        font-weight: $weight;
    }
}

@mixin transform($transform) {
    transform: $transform;
    -webkit-transform: $transform;
    -ms-transform: $transform;
}

@mixin richtext-block {
    .career-content-page-v2 {
        .richtextblock {
            padding: ($margin-default * 2) 0;

            p {
                &:last-child {
                    margin-bottom: 0;
                }
            }

            & + .rightimagerichtextblock, & + .richtextblock {
                padding-top: 0;
            }

            & + .careervaluesblock {
                .career-values-block {
                    padding-top: 0;
                }
            }

            & + .imagegalleryblock {
                .gallery-list {
                    margin-top: 0;
                }
            }
        }
    }
}

%custom-paragraph {
    .lead-paragraph, .lead-paragraph * {
        @extend %h3;

        a.btn-secondary-tinymce,
        a.btn-primary {
            line-height: 2.2rem;
        } 
    }
    
    .p-big, .p-big * {
        @extend %p-big;
    
        &.lrg-intro {
            @extend %h3;
        }

        a.btn-secondary-tinymce,
        a.btn-primary {
            line-height: 2.2rem;
        } 
    }

    .p-tag, .p-tag * {
        @include gt-body-text(small);
        letter-spacing: 0.2em;
        margin: 0 0 15px 0;
    
        a:not(.btn-primary) {
            color: inherit;
            text-decoration: none;
            &:hover {
                color: $color-dark-coral;
            }
        }
        
        a.btn-secondary-tinymce,
        a.btn-primary {
            line-height: 2.2rem;
        }
    }
}

@mixin richtext-image-block {
    @include richtext-block;

    .b-richtext--mage {
        overflow: hidden;
        padding: ($margin-default * 2) 0;

        .right-image {
            margin-bottom: 0;

            @include media-only-mobile {
                margin-bottom: $margin-default;
            }
        }

        p {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .c-content {
        &:not(.campaign-rich-text) {
            p, h2, h3 {
                font-size: $size-xx-large;
                line-height: 42px;
            }
        }
    }

    .rightimagerichtextblock {
        & + .richtextblock, & + .rightimagerichtextblock {
            padding-top: 0;
        }

        & + .careervaluesblock {
            .career-values-block {
                padding-top: 0;
            }
        }

        & + .imagegalleryblock {
            .gallery-list {
                margin-top: 0;
            }
        }
    }

    .careervaluesblock {
        & + .richtextblock, & + .rightimagerichtextblock {
            padding-top: 0;
        }

        & + .imagegalleryblock {
            .gallery-list {
                margin-top: 0;
            }
        }

        & + .careervaluesblock {
            .career-values-block {
                padding-top: 0;
            }
        }
    }

    .imagegalleryblock {
        & + .richtextblock, & + .rightimagerichtextblock {
            padding-top: 0;
        }

        & + .careervaluesblock {
            .career-values-block {
                padding-top: 0;
            }
        }

        & + .imagegalleryblock {
            .gallery-list {
                margin-top: 0;
            }
        }
    }
}

@mixin promo-block {
    .promo-block {
        .swiper-slide {
            margin-bottom: 32px;
        }

        .promo-block-item {
            margin-bottom: 0;
            margin-top: 0;

            .item__label {
                display: block;
                color: $color-dark-theme;
                padding: 0;
                margin-bottom: 8px;
            }

            .item__name {
                color: $color-core-purple;
                text-decoration: none;
                word-wrap: break-word;
                font-size: $size-large;
                font-weight: 400;

                @include hover {
                    color: $color-dark-coral;
                }

                @include transition(color);

                @include media-only-tablet {
                    margin-bottom: $margin-default;
                }
            }

            .item__summary {
                margin-bottom: 8px;

                @include media-only-tablet {
                    margin-bottom: $margin-default;
                    margin-top: 0px;
                }
            }

            .item__phone-number {
                color: $color-core-purple;
                margin-bottom: 8px;
                font-size: $size-regular;
            }

            .item__social {
                a {
                    margin-right: 10px;
                }

                [class*="icon-"] {
                    font-size: $size-large;
                }
            }

            &.no__link {
                .item__name {
                    cursor: auto;
                    @include hover {
                        color: $color-core-purple;
                    }
                }
            }
        }

        .campaign-slider {
            position: relative;
            overflow: hidden;

            .promo-block-item {
                min-height: 416px;
                padding-bottom: 0;
                background-repeat: no-repeat;
                background-position: right bottom;

                @include media-small-mobile {
                    background-size: 50%;
                }

                @include media-from-tablet {
                    background-size: 225px;
                }

                .item__name {
                    margin-bottom: 0;
                }

                .item__image {
                    padding: 0;
                    margin: 0;
                    right: 0;
                    position: absolute;
                    bottom: 0;
                    height: 219px;
                }

                @include media-to-tablet {
                    min-height: 360px;
                    margin-bottom: 0;
                }
            }
        }

        &.promo-listing-block {
            .campaign-slider {
                .swiper-slide {
                    display: flex;
                }

                .min-height-fix {
                    flex: 0 0 auto;
                    height: 416px; /* the "min-height" */
                    width: 1px; /* DEBUG */
                }

                .promo-block-item {
                    display: flex;
                    flex-direction: column;
                    flex: 1 1 auto;
                    flex-wrap: wrap;
                    height: 100%;

                    .item__image {
                        position: relative;
                        padding: 24px 0 !important;
                        left: 0;
                        width: 100%;
                        height: auto;
                        margin-top: auto !important;
                        flex-shrink: 0;
                    }
                }
            }
        }
    }
}

@mixin c-content {
    .c-content {
        padding: ($base-size) ($base-size/2);

        p {
            font-size: $size-large;
        }

        .c-content--inner {
            &.button-center {
                text-align: center;
            }

            a:not(.btn-primary) {
                @include link-underline;
            }

            .btn-primary {
                @include btn-vi-secondary-l1;
                margin: 0;                
            }
        }

        @include media-from-tablet {
            padding: ($base-size*2) $base-size;

            .c-content--inner {
                width: 100%;
                padding-left: $base-size/2;
                padding-right: $base-size/2;

                @include media-from-desktop-lg {
                    max-width: 1172px;
                }
            }

            .c-content--inner h2, .c-content--inner .h2 {
                margin-top: 0;
            }

            .c-content--inner.one {
                @include column-count-gap();
            }

            .c-content--inner.two {
                @include column-count-gap(2, 40);

                p {
                    margin-top: 0;
                }
            }

            .c-content--inner.three {
                @include column-count-gap(3, 40);

                p {
                    margin-top: 0;
                }
            }
        }

        @include media-only-mobile {
            .c-content--inner.one, .c-content--inner.two, .c-content--inner.three {
                @include column-count-gap();
            }
        }
    }

    .c-content__hero {
        padding: 0;
    }
}

@mixin custom-campaign-rich-text {
    .htmlblock {
        padding-top: $padding-default * 2;
        padding-bottom: $padding-default * 2;
    }
    // Begin CampaignRichTextBlock
    .campaign-rich-text-img {
        width: 100%;
    }
    .c-content .c-content--inner {
        .btn-primary {
            @include btn-vi-secondary-l1;
        }
    }
    @include media-from-tablet {
        .c-content .c-content--inner {
            position: relative;
            margin: 0 auto;
            * {
                break-inside: avoid;
            }
            .tinymce-wrapper {
                break-inside: auto;
                .btn-download {
                    a.download-pdf-link, a.btn-primary {
                        line-height: 2.2rem;
                        font-size: $size-regular;
                    }
                }
                .link-list-item {
                    a:not(.btn-primary) {
                        text-decoration: none;
                    }
                }
            }
        }
    }

    @include media-from-tablet {
        .c-content {
            .c-content--inner {
                &.one {
                    columns: 1;
                }

                &.two {
                    columns: 2;
                    column-gap: 40px;
                }

                &.three {
                    columns: 3;
                    column-gap: 40px;
                }

                &.button-center {
                    text-align: center;
                }
            }
        }
    }

    @media (min-width: 1300px) and (min-width: 768px) {
        .c-content .c-content--inner {
            max-width: $width-default * 36 + 20;
        }
    }
    // End CampaignRichTextBlock
    .campaign-rich-text, .campaign-multiarea-block {
        h2, h2:not(.sectionTag):not(.exp):not(.title):not(.category):not(.fcb__title):not(.intro-title) {
            font-size: $size-x-large;
            @include media-from-lgtablet {
                font-size: $size-xx-large;
            }
        }

        a.btn-cta {
            @include btn-vi-primary-l1;
        }

        .tinymce-wrapper {
            a:not(.btn-primary) {
                @include link-underline;
                @include hover {
                    color: $color-dark-coral;
                }

                &:has(img):has([style*="float: right"]), &:has(img):has([style*="float: left"]) {
                    display: inline;
                }
                &.video-block-v2__play {
                    display: flex;
                    &:hover {
                        color: $color-core-purple;
                    }
                }
            }

            .share-this-page-block  a {
                display: flex;
            }

            .link-list-item {
                a:not(.btn-primary) {
                    text-decoration: none;
                }
            }
        }

        // Fix for case editor put Insight Hub Overlay to tinyMce.
        &[class*="bg-inverse__"] {
            .c-content--inner {
                .hub-overlay, .next-article-item {
                    .btn-primary {
                        @include btn-vi-primary-l1;
                        &::after {
                            content: none;
                        }
                    }
                }
            }
        }
        
        &.bg-inverse__purple, &.bg-inverse__dark-purple {
            a.btn-cta {
                @include btn-vi-primary-d1;
            }
            .c-content--inner {
                h2, h3, p {
                    color: $color-white
                }

                a.btn-secondary-tinymce,
                a.btn-primary {
                    @include btn-vi-secondary-d1($color-white, $color-core-purple);
                }
            }
        }

        &.bg-inverse__yellow {
            .c-content--inner {
                &.action-button__position {
                    a.btn-cta {
                        @include btn-vi-primary-d2;
                    }
                }

                a.btn-secondary-tinymce,
                a.btn-primary {
                    @include btn-vi-secondary-l1($color-white);
                }
            }
        }

        &.bg-inverse__teal {
            .c-content--inner {
                &.action-button__position {
                    a.btn-cta {
                        @include btn-vi-primary-d2;
                    }
                }

                a.btn-secondary-tinymce,
                a.btn-primary {
                    @include btn-vi-secondary-l1($color-white);
                }
            }
        }

        &.c-content__hero {
            .c-content--inner {
                padding: $padding-default $padding-default/2;

                &.has-button__text {
                    padding-bottom: $padding-default/2;
                    & + .action-button__position {
                        padding-bottom: $padding-default*2;
                    }
                }

                &.action-button__position {
                    padding-top: 0;
                }
            }
        }

        .c-content--inner {
            // remove label selector to avoid override CSS on form v2
            p, li, span, pre, blockquote, legend, td, th {
                font-size: $size-large;
                line-height: $size-xx-large;
            }

            @extend %custom-paragraph;
            &.action-button__position {
                padding-top: 0;

                a.btn-cta {
                    margin-bottom: 0;
                    text-decoration: none;
                }
            }

            iframe {
                transform: translate3d(0,0,0);
            }
        }

        @include media-from-tablet {
            &.c-content__hero {
                .c-content--inner {
                    padding: 64px 16px;
                    width: calc(100% - 64px);
                }
            }
        }
    }
}

@mixin map-links-icon {
    &.icon-phone-new, &.icon-printer_eps, &.icon-contact_form {
        font-size: $size-very-large;
    }

    &.icon-mail-new {
        font-size: $size-tiny;
    }

    &.icon-contact_form {
        padding-left: 1px;
        margin-right: 17px;
    }
}

@mixin column-count-gap ($count: 1, $gap: 0) {
    -webkit-column-count: $count;
    -moz-column-count: $count;
    columns: $count;

    @if ($gap > 0) {
        -webkit-column-gap: $gap * 1px;
        -moz-column-gap: $gap * 1px;
        column-gap: $gap * 1px;
    }
}

@mixin appearance($appearance: none) {
    -webkit-appearance: $appearance;
    -moz-appearance: $appearance;
    appearance: $appearance;
}

@mixin truncate-text-by-line($max-lines: 3, $text-height: #{$size-very-large}) {
    @if $text-height != -1 {
        max-height: calc(#{$text-height} * #{$max-lines});
    }

    display: block;
    display: -webkit-box;
    -webkit-line-clamp: $max-lines;
    overflow: hidden;
    text-overflow: ellipsis;
    /* autoprefixer: off */ -webkit-box-orient: vertical; /* autoprefixer: on */
}


@mixin prominent-link {
    a:not([class*="btn"]):not(.button-vi):not(.subscribe-now):not([class*="link"]):not([class*="js-close-overlay"]) {
        @include link-underline;
    }
}

@mixin link-underline {
    text-decoration: underline;
    text-underline-offset: $underline-offset;
}

%hidden {
    display: none;
}

%hide-on-mobile {
    @include media-only-mobile {
        display: none !important;
    }
}

%hide-to-desktop {
    @include media-to-desktop {
        display: none !important;
    }
}

@mixin spinner-mixin {
    @keyframes anim-rotate {
        0% {
            transform: rotate(0);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    &.icon-spinner3 {
        animation: anim-rotate 1s infinite steps(12);
    }
}


@mixin sort-dropdown {
    display: inline-flex;
    align-items: center;
    select {
        background: $color-white;
        line-height: 2rem;
        padding-right: $padding-default * 1.5;
        border-width: 2px;
        cursor: pointer;
        &:not(:focus) {
            border-color: transparent;
        }

        & + svg {
            position: absolute;
            right: $margin-default * 0.375;
            top: 50%;
            translate: 0 -50%;
            pointer-events: none;
        }
    }

    .form-select {
        margin: 0;
        &::after {
            display: none;
        }
    }

    .sort-title {
        margin-right: $margin-default * 0.25;        
    }
}

@mixin ibcd-location-svg-icon {
    .icon-svg {
        width: 1.7rem;
        min-width: 1.7rem;
        height: 1.7rem;
        margin-right: $margin-default/4;
        display: inline-block;
        vertical-align: middle;

        img,
        svg {
            width: 100%;
            height: 100%;
            display: block;
            max-height: 1.7rem;
        }

        .social-icon__vi {
            --social-icon-svg-width: 1.7rem;
            --social-icon-svg-height: 1.7rem;
        }
    }
}

@mixin page-text-area-container {
    @include media-from-lgtablet {
        max-width: 75rem;
        margin-left: auto;
        margin-right: auto;
    }
}

@mixin article-sumary-contents($paragraph-type) {
    .article-summary {
        background: none;
        max-width: 100%;
        padding: 0;
        margin-bottom: $base-size * 1.5;
        .summary-title {
            @include gt-body-text("small");
            text-transform: uppercase;
            color: $color-rolling;
            margin-bottom: $margin-default*3/4;
            letter-spacing: 0.28rem;
        }
        .keypoints {
            margin: 0;
            padding: 0;
            li {
                list-style: none;
                padding-left: 0;
                margin-top: $margin-default/2;
                &:first-child {
                    margin-top: 0;
                }
                &:before {
                    content: none;
                }
                a.keypoint-link {
                    @include gt-small-title(true, "regular");
                    color: $color-core-purple;
                    @include hover {
                        @include link-underline;
                    }
                }
            }
        }
    }
}


@mixin tinyMceTypo($paragraph-type: "", $force-font-weight: "", $heading-font-weight: "semibold", $selectors: $tinymce-selectors-full) {
    h2 {
        @include gt-subtitle(true, $heading-font-weight);
        #{$tinymce-selectors-full}, strong {
            @include gt-subtitle(true, $heading-font-weight);
        }
    }

    h3 {
        @include gt-small-title(true, $heading-font-weight);
        #{$tinymce-selectors-full}, strong {
            @include gt-small-title(true, $heading-font-weight);
        }
    }

    h4 {
        @include gt-body-text("big", $heading-font-weight);
        #{$tinymce-selectors-full}, strong {
            @include gt-body-text("big", $heading-font-weight);
        }
    }

    h2 + p,
    p + h2 {
        margin-top: $margin-default;
    }

    h3 + p, h4 + p,
    p + h3, p + h4 {
        margin-top: $margin-default * 0.75;
    }



    #{$selectors} {
        @include gt-body-text($paragraph-type, $force-font-weight);

        &.lead-paragraph, &.lead-paragraph * {
            @if $force-font-weight {
                @include gt-small-title(true, $force-font-weight);
            } @else {
                @include gt-small-title(true, "regular");
            }
        }

        &.p-big {
            @if $force-font-weight {
                @include gt-body-text("big", $force-font-weight);
            } @else {
                @include gt-body-text("big", "regular");
            }
        }

        &.lead-paragraph,
        &.p-big {
            sup, sub {
                font-size: $size-regular;
            }
        }

        &.p-tag, &.p-tag * {
            @include gt-body-text("small");
        }
        &.p-tag {
            sup, sub {
                font-size: $size-x-tiny;
            }
        }
    }

    strong span,
    strong a,
    b, strong {
        @include gt-body-text($paragraph-type);
        @include gt-font-weight("bold", "gt-body-text");
    }

    sup, sub {
        @if $paragraph-type == "big" {
            font-size: $size-regular;
        }
        @if $paragraph-type == "" {
            font-size: $size-tiny;
        }
        @if $paragraph-type == "small" {
            font-size: $size-x-tiny;
        }
    }

    ul {
        padding-left: $padding-default*3/4;
        li {
            span.maker-list__item {
                padding-left: $padding-default;
                display: table;
                position: relative;
                
                &:before {
                    display: block;
                    content: "";
                    width: $base-size/4;
                    height: $base-size/4;
                    background: $color-black;
                    border-radius: $base-size;
                    position: absolute;
                    left: 0;
                    top: $base-size/2 - 2;
                }
            }
        }
    }

    ol {
        counter-reset: item;
        padding-left: $padding-default*3/4;
        li {
            $counter: counter(item);
            counter-increment: item;
            span.maker-list__item {
                padding-left: $padding-default*3/4;
                display: table;
                position: relative;

                &:before {
                    content: $counter ".";
                    display: block;
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
        }
    }
}



@mixin tinymce-typo-without-block($paragraph-type: "", $force-font-weight: "", $heading-font-weight: "semibold", $selectors: $tinymce-selectors-full) {
    #{$tinymce-selectors},
    & > {
        @include tinyMceTypo($paragraph-type, $force-font-weight, $heading-font-weight, $selectors);
    }
}

@mixin anchor-scroll-offset($mobile-offset: $margin-default, $desktop-offset: $margin-default * 2) {
    [id] {
        scroll-margin-top: calc(var(--header-height) + #{$mobile-offset});
        @include media-from-desktop {
            scroll-margin-top: calc(var(--header-height) + #{$desktop-offset});
        }
    }
}