%icon-linkedin-c:hover, %icon-linkedin-c:active {
    position: relative;
    .path2, .path3, .path4 {
        position: absolute;
        top: 0;
        left: 0;
    }
    &:before {
        content: "";
    }
    .path1:before {
        content: "\e926";
        color: $color-black;
    }
    .path2:before {
        content: "\e927";
    }
    .path3:before {
        content: "\e928";
    }
    .path4:before {
        content: "\e929";
    }
}
@mixin icon-linkedin-c-color($primary-color: $color-black, $secondary-color: $color-white) {
    .icon-linkedin-c {
        color: $primary-color;
        &:hover, &:active {
            .path1:before {
                color: $primary-color;
            }
            .path2:before, .path3:before, .path4:before {
                color: $secondary-color;
            }
        }
    }
}
