@mixin set-font($size: false, $height: false, $weight: false, $spacing: false) {
    @if $size { font-size: $size; }
    @if $height { line-height: $height; }
    @if $weight { font-weight: $weight; }
    @if $spacing {letter-spacing: $spacing}
}

@mixin reset-list {
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
            margin: 0;
            padding: 0;
        }
    }
}

