@mixin overrideLanguage($family) {
    body {
        font-family: $family;
        .richtext p,
        .richtext li,
        .richtext .p-big {
            font-family: $family;
            font-size: $size-regular;
            line-height: 2.4rem;
        }

        .richtext p, .tinymce-page__text p {
            &.p-tag,
            &.lead-paragraph {
                font-family: $family;
            }
        }

        .richtext {
            p {
                margin-bottom: 20px;
            }

            &.no-narrow-text {
                p:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}