
@import "../../settings/fonts";

$form-selectors: '.rd-form, .ms365-form, .marketo-embed-form, .EPiServerForms, .hbspt-form, #up-form';

@mixin form-input {
    display: block;
    border: var(--form-field-input-border-width, 2px) solid var(--form-field-input-border-color, $color-core-purple);
    border-radius: var(--form-field-input-border-radius, 0);
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: var(--form-field-input-background-color, $color-white);
    padding: 1rem 2rem;
    font-size: $size-regular;
    color: var(--form-field-label-text-color, $color-core-purple);
    margin-bottom: ($margin-default / 4);
    max-width: 100%;
    outline: none;

    &.full-width {
        width: 100%; // min-width: 100%;
    }

    @include media-only-mobile2 {
        width: 100%;
    }

    &:focus-visible, &:focus {
        border-color: var(--color-focus-visible, $color-core-purple);
    }
}

@mixin validation-error {
    position: relative;
    display: inline-block;
    max-height: $size-regular * 6;
    margin-top: $margin-default / 4;
    opacity: 1;
    color: var(--form-validation-error-color, $color-dark-coral);
}

@mixin checkbox-tick {
    content: ''!important;
    width: 1.4rem;
    height: 0.75rem;
    position: absolute;
    left: 3px;
    top: 0;
    margin-top: 0.4rem;
    border: 0.2rem solid $color-core-purple;
    border-top: none;
    /*rtl:ignore*/
    border-right: none;
    background: transparent;
    opacity: 1;
    /*rtl:ignore*/
    transform: rotate(-55deg) skewX(-15deg);
}

@mixin marketo-recapcha {
    .g-recaptcha-container {
        > iframe {
            display: none;
        }

        textarea[name="g-recaptcha-response"] {
            display: none;
            border: 0;
        }
        .captcha-error {
            position: relative;
            margin: $margin-default*0.25 0 0;
            padding: 0;
            color: var(--form-validation-error-color, $color-dark-coral);
            @include gt-body-text('small');
        }
    }
}

// Create a mixin for checkbox/radio icon styles
@mixin form-choice-icon {
    & + .tinymce-wrapper,
    &.hs-input + span, // Hubspot form checkbox
    &.bricks-form__field__option__input + .bricks-form__field__option-icon,
    & + label,
    & + .choice-icon,
    & + input[type="hidden"] + label {
        &:before,
        &:after {
            display: block;
            content: '';
            position: absolute;
        }
        &:before {
            top: 0;
            left: 0;
            width: var(--form-checkbox--font-size, 2rem);
            height: var(--form-checkbox--font-size, 2rem);
            border: 0.1rem solid var(--form-field-input-border-color, $color-core-purple);
            background: var(--form-checkbox-background-color, $color-white);
            box-shadow: none;
            margin: 0;
            border-radius: var(--form-field-input-border-radius, 0.4rem);
        }

        &:after {
            top: var(--form-checkbox-icon-position, 0.4rem);
            left: var(--form-checkbox-icon-position, 0.4rem);
            width: var(--form-checkbox-icon--font-size, 1.2rem);
            height: var(--form-checkbox-icon--font-size, 1.2rem);
            opacity: 0;
            transition: opacity 0.2s ease-in;
            background: var(--form-checkbox-icon-background-color, $color-light-purple);
            border-radius: var(--form-checkbox-icon-border-radius, 0.2rem);
        }
    }

    &:checked + .tinymce-wrapper,
    &.hs-input:checked + span,
    &.bricks-form__field__option__input:checked + .bricks-form__field__option-icon,
    &:checked + label,
    &:checked + .choice-icon,
    &:checked + input[type="hidden"] + label {
        &:before {
            border-color: var(--form-checkbox-border-color-checked, $color-core-purple);
        }
        &:after {
            opacity: 1;
        }
    }

    &:focus-visible + .tinymce-wrapper,
    &.hs-input:focus-visible + span,
    &.bricks-form__field__option__input:focus-visible + .bricks-form__field__option-icon,
    &:focus-visible + label,
    &:focus-visible + .choice-icon,
    &:focus-visible + input[type="hidden"] + label {
        &:before {
            border-width: 0.2rem;
        }
    }

    .FormChoice.ValidationFail & {
        & + .tinymce-wrapper,
        &.hs-input + span, // Hubspot form checkbox
        & + label,
        & + .choice-icon,
        & + input[type="hidden"] + label {
            &:before{
                border-color: var(--form-validation-error-color, $color-dark-coral);
            }
        }
    }
}

$default-input-selectors: 'input[type="text"], input[type="email"], input[type="number"], input[type="url"], input[type="tel"], input[type="password"], input[type="search"], input[type="date"], input[type="datetime-local"], input[type="month"], input[type="time"], input[type="week"], textarea, select';

@mixin form-v2-select($label-selector: '> label') {
    position: relative;
    &::before,
    &::after {
        display: block;
        content: "";
        position: absolute;
        pointer-events: none;
        z-index: 3;
    }

    &::before {
        top: 1.3rem;
        right: 1.5rem;
        width: $base-size*0.75;
        height: $base-size*0.75;
        border-radius: 50%;
        background-color: var(--form-select-icon-background-color, $color-light-grey);
    }
    &::after {
        top: 2.2rem;
        right: 2.4rem;
        width: 0.5rem;
        height: 0.5rem;
        border: none;
        border-left: 0.1rem solid $color-core-purple;
        border-bottom: 0.1rem solid $color-core-purple;
        transform: rotate(-45deg);
    }
    
    &:has(select[multiple]) {
        &::before,
        &::after {
            display: none;
        }
    }

    select {
        padding: $padding-default*0.4375 $padding-default*0.625;
        outline: none;
        color: transparent;
        @include appearance();
        @include transition-mix(all 0.2s ease);
        
        &[multiple] {
            padding-top: $padding-default*0.5;
            padding-bottom: $padding-default*0.5;
            color: var(--form-field-input-text-color, $color-black);
            option {
                padding-left: 0;
                &:disabled[value=''] {
                    opacity: 0;
                }
            }
        }

        &:focus, &:focus-visible {
            border-color: var(--color-focus-visible, $color-core-purple);
        }

        &::-ms-expand {
            display: none;
        }
    }
}

@mixin form-floating-label($label-selector: '> label', $input-selector: $default-input-selectors) {
    position: relative;
    
    #{$input-selector} {
        @include placeholder {
            color: var(--form-field-label-text-color, $color-black);
            opacity: 0;
        }
        &:focus {
            @include placeholder {
                color: transparent;
            }
        }
    }

    #{$label-selector} {
        position: absolute;
        z-index: 2;
        top: #{$base-size*0.5};
        left: #{$base-size*0.625};
        margin: 0;
        min-width: initial;
        width: auto;
        color: var(--form-field-label-text-color, $color-dark-theme);
        background-color: var(--form-field-input-background-color, $color-white);
        pointer-events: none;
        max-width: calc(100% - 4rem);
        max-height: 2.6rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        @include gt-body-text('small');
        @include transition-mix(all 0.2s ease);
        &.empty {
            + #{$input-selector} {
                @include placeholder {
                    opacity: 1;
                }
            }
        }
    }

    &.ValidationFail {
        #{$input-selector},
        select {
            border-color: var(--form-validation-error-color, $color-dark-coral);
        }
    }

    &:hover:not(.focus) {
        #{$label-selector} {
            color: var(--form-field-label-text-color-hover, $color-black);
        }
    }
    .filled,
    &.filled {
        #{$input-selector} {
            border-color: var(--form-input-border-color-filled, $color-black);
        }
        #{$label-selector} {
            color: var(--form-field-input-text-color, $color-black);
        }
    }

    .focus,
    &.focus {
        #{$input-selector} {
            border-color: var(--form-input-border-color-focus, $color-core-purple);
        }
        #{$label-selector} {
            color: var(--form-field-label-text-color-focus, $color-core-purple);
        }
    }

    .filled,
    .focus,
    &.filled,
    &.focus,
    &.multiple.filled,
    &.multiple.focus,
    &.bricks-form__field.with-select-flags,
    &.bricks-form__field-upload,
    &.FormFileUpload {
        #{$label-selector} {
            top: #{-$base-size*0.5625};
            left: #{$base-size*0.5};
            width: auto;
            border-radius: 2px;
            padding: $padding-default*0.125 $padding-default*0.125 0;
            background-color: var(--form-label-background-color,$color-grey-bg-40);
            pointer-events: initial;
        }

        #{$input-selector} {
            @include placeholder {
                opacity: 1;
            }
        }
    }
}

@mixin form-v2-theme($theme-type) {
    --form-field-input-border-width: 0.1rem;
    --form-field-input-border-radius: #{getBorderRadius(0)};
    --form-upload-button-background-color: #{$color-white};
    
    @if $theme-type == 'purple' {
        --form-field-input-background-color: #{$color-dark-purple};
        --form-field-input-text-color: #{$color-white};
        --form-field-input-border-color: #{$color-white};
        --form-input-border-color-focus: #{$color-white};
        --form-input-border-color-filled: #{$color-white};
        --form-field-label-text-color: #{$color-white};
        --form-field-label-text-color-hover: #{$color-white};
        --form-field-label-text-color-focus: #{$color-white};
        --color-focus-visible: #{$color-white};
        --form-range-input-background-color: #{$color-white};
        --form-range-thumb-background-color: #{$color-white};
        --form-choice-label-text-color: #{$color-white};
        --form-checkbox-border-color-checked: #{$color-white};
        --form-select-icon-background-color: #{$color-light-purple};
        --form-step-progress-bar-background-color: #{$color-white};
        --form-step-progress-bar-background-color-active: #{$color-light-purple};
        --form-step-progress-bar-background-color-current: #{$color-core-purple};
        --form-step-progress-bar-border-color-current: #{$color-white};
        --form-field-button-text-color: #{$color-white};
        --form-range-thumb-focus-background-color: #{$color-light-purple};
        --form-validation-error-color: #{$color-light-coral};
        --form-rangeslider-tooltip-text-color: #{$color-core-purple};
        --form-rangeslider-tooltip-background-color: #{$color-white};
    } @else {
        --form-field-input-text-color: #{$color-black};
        --form-field-input-border-color: #{$color-dark-grey};
        --form-input-border-color-focus: #{$color-core-purple};
        --form-input-border-color-filled: #{$color-dark-grey};
        --form-field-input-background-color: #{$color-white};
        --form-field-label-text-color: #{$color-dark-theme};
        --form-field-label-text-color-hover: #{$color-black};
        --form-field-label-text-color-focus: #{$color-core-purple};
        --color-focus-visible: #{$color-core-purple};
        --form-range-input-background-color: #{$color-dark-grey};
        --form-range-thumb-background-color: #{$color-dark-theme};
        --form-choice-label-text-color: #{$color-black};
        --form-checkbox-border-color-checked: #{$color-core-purple};
        --form-select-icon-background-color: #{$color-light-grey};
        --form-step-progress-bar-background-color: #{$color-dark-grey};
        --form-step-progress-bar-background-color-active: #{$color-light-purple};
        --form-step-progress-bar-background-color-current: #{$color-white};
        --form-step-progress-bar-border-color-current: #{$color-core-purple};
        --form-field-button-text-color: #{$color-dark-coral};
        --form-range-thumb-focus-background-color: #{$color-core-purple};
        --form-validation-error-color: #{$color-dark-coral};
        --form-rangeslider-tooltip-text-color: #{$color-white};
        --form-rangeslider-tooltip-background-color: #{$color-core-purple};
    }

    @if $theme-type == 'white' {
        --form-label-background-color: #{$color-white};
        --form-upload-button-background-color: #{$color-light-grey};
    } @else if $theme-type == 'purple' {
        --form-label-background-color: #{$color-core-purple};
        --form-checkbox-background-color: #{$color-core-purple};
    } @else {
        --form-label-background-color: #{$color-grey-bg-40};
    }
}



/// use this input style for both internal and external forms
@mixin form-v2-input {
    margin: 0;
    padding: $padding-default*0.4375 $padding-default*0.625;
    width: 100%;
    border: var(--form-field-input-border-width, 1px) solid var(--form-field-input-border-color, $color-core-purple);
    background-color: var(--form-field-input-background-color, $color-white);
    border-radius: var(--form-field-input-border-radius, #{getBorderRadius(0)});
    @include gt-body-text('small');
    color: var(--form-field-input-text-color, $color-black);
    outline: none;
    box-shadow: none;
    @include appearance();
    @include transition-mix(all 0.2s ease);
    :-internal-autofill-selected {
        background-color: var(--form-input-background-color-filled, $color-white);
    }
    &[type="date"] {
        height: $base-size*1.625;
    }
}

@mixin form-v2-theme-set($theme-type: 'grey') {
    
    #{$form-selectors} {
        @include form-v2-theme($theme-type);
    }
}