@charset "UTF-8";

// Default Margin and Default Padding Variable
$base-size:         32px !default;
$margin-default:    $base-size;
$padding-default:   $base-size;
$height-default:    $base-size;
$width-default:     $base-size;

$height-promoblock-sml: $base-size * 8;
$height-promoblocks:    $base-size * 17;

$nav-v3-height: 7rem;
/*
 * Epi Form sizes
 */

$form-input-width: 554px;
$epiform-size-large: $base-size * 3/4;
$epiform-size-small: $base-size * 3/8;

$overlay-opacity: 0.8;

$button-vi-border-radius: 0.4rem;

$banner-title-size: 600px;
$hero-banner-title-max-width: 83%; 
$banner-small-title-max-width: 460px;
$banner-description-size: 350px;
$letter-spacing: 1.6px;
$input-border-radius: $base-size;
$dropdown-expanded-border-radius: 2rem;

$line-length: 948px;