@mixin background-overlay-transparent-on-mobile {
    @include media-to-lgtablet {
        &.background-transparent-mobile {
            [class*="col"] {
                padding-top: $padding-default;
                padding-bottom: $padding-default;
            }

            .gt-category {
                margin-bottom: $margin-default*0.5;
                text-transform: none;
            }
            .title {
                margin: 0;
                max-width: 82.5%;
            }
            p.reveal-item {
                margin: 0;
                max-width: none;
            }
            .btn-primary,
            .cta-button {
                margin: $margin-default/2 0 0;
            }
            &:not(.bg-gradient-animate):not(.mobius) {
                .background-underlay {
                    display: none;
                }
            }
            .background:after {
                background: $background-gradient-to-dark;
                opacity: 1;
            }
        }
    }
}