﻿@charset "UTF-8";
// Default Color Variables
$color-white: #fff; //White

//Purple colors
$color-dark-purple: #2b144d; // Dark Purple
$color-grape: #3a205f; // Using only in figure carousel block but swiper has been removed.
$color-core-purple: #4f2d7f; // Core Purple
$color-dark-purple-80: #554370; 
$color-core-purple-tinted: #dcd5e5; // Light Purple 20%
$color-light-purple: #a06dff; // $color-bright-purple => $color-light-purple because of gulp-rtlcss

//Dark colors
$color-black: #000;
$color-light-dark: #070707;
$color-dark-theme: #666666;
$color-rolling: $color-dark-theme;

//Grey colors
$color-dark-grey: #ccc4bd;
$color-light-grey: #f2f0ee;
$color-mid-grey: #e0dcd7;
$color-grey-bg-60: #eceae7;
$color-grey-bg-40: #f2f1ef;


$color-dark-coral: #ce2c2c; // Dark Coral
$color-coral: #ff5149; // Coral
$color-light-coral: #ff9792;

$color-green: #9bd732; // Need to check more
$color-yellow: #ffc23d;
$color-teal: #00a4b3; // Teal
$color-teal-tinted: #ccedf0; // Teal 20%
$color-focus-visible: $color-teal;
$color-focus-visible-teal-bg: $color-core-purple;

// Generic Elements Colors
$text-color : $color-black;
$link-color : $color-dark-coral;
$link-color-hover : $color-dark-coral;
$btn-primary-color : $color-dark-coral;
$btn-primary-light-color : $color-white;
$btn-secondary-color : $color-dark-coral;
$news-date-color : $color-rolling;
$news-title-color : $color-core-purple;
$featured-content-title-color: $color-core-purple;
$related-content-item-title : $color-core-purple;
$related-content-read-more : $color-dark-coral;
$expert-promo-name-link : $color-core-purple;
$expert-promo-category : $color-rolling;
$box-shadow-color : $color-mid-grey;

$background-filled-white: $color-white;
$background-filled-black: $color-black;
$background-filled-purple: $color-core-purple;
$background-overlay-dark-theme: rgba($color-black, 0.35);
$background-overlay-dark-theme-active: rgba($color-black, 0.65);
$columns-slider-overlay-dark-theme-active: rgba($color-dark-purple, 0.65);
$background-overlay-light-theme: rgba($color-white, 0.35);
$background-overlay-light-theme-active: rgba($color-white, 0.65);
$background-overlay-purple-theme: rgba($color-core-purple, 0.8);
$background-overlay-purple-light: rgba($color-dark-purple, 0.4);
$background-gradient-to-dark: linear-gradient(to bottom, transparent, rgba($color-black, 0.65));
$background-gradient-to-light: linear-gradient(to bottom, transparent, $color-white);
