//@include setZIndex($header)       => z-index: 79
//@include setZIndex($header, 1)    => z-index: 80
//@include setZIndex($header, -1)   => z-index: 78
$z-header: 79;
$z-overlay: $z-header + 1;
$z-overlay-content: 101;
$z-cookie-consent: 103;

// Change this z-index will impact filter and sticky filter in T4/T9/T36/T47/T51/T53
$nav-v2-z-index: 12;

@mixin setZIndex($zindex, $diff: 0) {
    z-index: $zindex + $diff;
}