﻿@charset "UTF-8";

/*
    Media Queries Mixins

    small-mobile             === (max-width: 479px)
    large-mobile             === (min-width: 480px) and (max-width: 767px);

    media-only-mobile        === (max-width: 767px);
    media-only-tablet        === (min-width: 768px) and (max-width: 1023px);
    media-only-dekstop       === (min-width: 1024px) and (max-width: 1299px);
    media-only-dekstop-lg    === (min-width: 1200px);

    media-from-mobile        === (min-width: 480px);
    media-from-tablet        === (min-width: 768px);
    media-from-desktop       === (min-width: 1024px);
    media-from-desktop-lg    === (min-width: 1400px);

*/

@mixin media-x-tiny-mobile {
    @media (max-width: #{$screen-x-tiny}) {
        @content;
    }
}

@mixin media-only-x-tiny-mobile-to-lgtablet {
    @media (min-width: #{$screen-x-tiny}) and (max-width: #{$screen-lgtab - 1}) {
        @content;
    }
}

@mixin media-tiny-mobile {
    @media (max-width: 399px) {
        @content;
    }
}

@mixin media-small-mobile {
    @media (max-width: #{$screen-xs - 1}) {
        @content;
    }
}

@mixin media-large-mobile {
    @media (min-width: #{$screen-xs}) and (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin media-only-mobile {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin media-only-mobile2 {
    @media (max-width: #{$screen-sm}) {
        @content;
    }
}

@mixin media-only-tablet {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-only-smtablet {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-lgtab - 1}) {
        @content;
    }
}


@mixin media-only-desktop {
    @media (min-width: #{$screen-md}) and (max-width: #{$screen-md-max}) {
        @content;
    }
}

@mixin media-only-desktop-sm {
    @media (min-width: #{$screen-md}) and (max-width: #{$screen-lg-md - 1}) {
        @content;
    }
}

@mixin media-from-mobile {
    @media (min-width: #{$screen-xs}) {
        @content;
    }
}

@mixin media-to-tablet {
    @media (max-width: #{$screen-xs-max}) {
        @content;
    }
}

@mixin media-to-tablet2 {
    @media (max-width: #{$screen-xs-max + 1}) {
        @content;
    }
}


@mixin media-from-tablet {
    @media (min-width: #{$screen-sm}) {
        @content;
    }
}

@mixin media-only-tablet-to-lgtablet {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-lgtab -1}) {
        @content;
    }
}

@mixin media-to-lgtablet {
    @media (max-width: #{$screen-lgtab - 1}) {
        @content;
    }
}

@mixin media-from-lgtablet {
    @media (min-width: #{$screen-lgtab}) {
        @content;
    }
}

@mixin media-from-tablet-to-lgtablet {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-lgtab}) {
        @content;
    }
}

@mixin media-from-tablet-to-desktop {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-only-lgtablet-to-desktop {
    @media (min-width: #{$screen-lgtab}) and (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-to-desktop {
    @media (max-width: #{$screen-sm-max}) {
        @content;
    }
}

@mixin media-from-lgtablet-to-desktop-lg {
    @media (min-width: #{$screen-lgtab}) and (max-width: #{$screen-md-max}) {
        @content;
    }
}

@mixin media-from-tablet-to-desktop-lg {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-lg}) {
        @content;
    }
}

@mixin media-from-tablet-to-desktop-md {
    @media (min-width: #{$screen-sm}) and (max-width: #{$screen-lg-md - 1}) {
        @content;
    }
}

@mixin media-from-lgtablet-to-desktop-md {
    @media (min-width: #{$screen-lgtab}) and (max-width: #{$screen-lg-md - 1}) {
        @content;
    }
}

@mixin media-from-desktop {
    @media (min-width: #{$screen-md}) {
        @content;
    }
}

@mixin media-from-desktop-md {
    @media (min-width: #{$screen-lg-md}) {
        @content;
    }
}

@mixin media-to-desktop-md {
    @media (max-width: #{$screen-lg-md - 1}) {
        @content;
    }
}

@mixin media-from-desktop-lg {
    @media (min-width: #{$screen-lg-min}) {
        @content;
    }
}

@mixin media-to-desktop-lg {
    @media (max-width: #{$screen-md-max}) {
        @content;
    }
}

@mixin media-only-desktop-md {
    @media (min-width: #{$screen-lg-md}) {
        @content;
    }
}

@mixin media-from-desktop-lg-extra {
    @media (min-width: #{$screen-lg-desktop-extra}) {
        @content;
    }
}

@mixin mobile-or-touchable {
    @media(max-width: #{$screen-sm-max}), (pointer:coarse) {
        @content;
    }
}

@mixin pointer-coarse {
    @media (pointer: coarse) {
        @content;
    }
}

@mixin media-only-ie {
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        @content;
    }
}

@mixin media-only-safari {
    @supports (-webkit-touch-callout: none) {
        @content;
    }
}

// Using only for safari on MAC.
@mixin media-only-safari-on-mac {
    @supports (hanging-punctuation: first) and (-webkit-appearance: none) {
        @content;
    }
}

@mixin media-only-firefox-desktop {
    @-moz-document url-prefix() {
        @content;
    }
}

@mixin media-from-desktop-xl {
    @media (min-width: 1680px) {
        @content;
    }
}

@mixin media-to-desktop-xl {
    @media (max-width: 1680px) {
        @content;
    }
}

@mixin support-SVG-animation($isNotSupport: false) {
    @if $isNotSupport {
        @supports not (d: path("M0 0")) {
            @content;
        }
    }
    @else {
        @supports (d: path("M0 0")) {
            @content;
        }
    }
}

@mixin media-nontouch-devices {
    @media (hover: hover) {
        @content;
    }
}

@mixin media-touch-devices {
    @media (hover: none) {
        @content;
    }
}

@mixin media-firefox-only {
    @-moz-document url-prefix() {
        @content;
    }
}

@mixin media-not-firefox {
    @supports not( -moz-appearance:none ){
        @content;
    }
}

@mixin media-only-landscape {
    @media (min-aspect-ratio: 3/2) {
        @content;
    }
}

@mixin media-only-portrait {
    @media (max-aspect-ratio: 3/2) {
        @content;
    }
}