//DO NOT USE EXTEND, BECAUSE CANNOT USE MEDIA QUERY INSIDE A EXTEND

@mixin trigger-svg-animation-vi {
    .svg-animation-vi {
        --svg-animate-delay: 0.3s;
        --svg-animate-name: svg-animate;
        --svg-animate-name2: svg-animate2;
        --svg-animate-duration: 1s;
        opacity: 1;
        @include support-SVG-animation("not") {
            translate: var(--fallback-translate-safari) 0;
        }
    }
}

@mixin button-text-vi {
    border-radius: $base-size/8;
    padding: 1.4rem 4rem 1.4rem 4.6rem;
    cursor: pointer; 
    position: relative; 
    font-size: $size-regular;
    border: 0.2rem solid $color-dark-coral;
    font-weight: 400;
    display: inline-flex; 
    vertical-align: middle; 
    transition: all 0.3s linear; 
    color: var(--button-vi-text-color, #{$color-dark-coral});
    justify-content: center;
    text-decoration: none;
    @include media-small-mobile {
        width: 100%;
        box-sizing: border-box;
        padding: 1.4rem 3.2rem;
    }

    .svg-animation-vi {
        width: 3rem;
        min-width: 3rem;
        height: 2.4rem;
        --svg-animate-duration: 0.3s;
        --svg-animate-delay: 0s;
        display: inline-flex;
        vertical-align: middle;
        pointer-events: none;
    }

    @include hover {
        text-decoration: none;
        .button-vi-text {
            transform: translate3d(0, 0, 0);
        }
        @include trigger-svg-animation-vi;
    }

    .button-vi-text {
        color: var(--button-vi-text-color, #{$color-white});
        display: inline-flex;
        vertical-align: middle;
        font-size: $size-regular;
    }
}

@mixin button-vi-primary {
    @include button-text-vi;
    background: $color-dark-coral;
    align-items: center;
    .svg-animation-vi {
        color: var(--button-vi-text-color, #{$color-white});
        opacity: 0;
        --svg-animate-name: svg-animate-out-1;
        --svg-animate-name2: svg-animate-out-2;
        @include support-SVG-animation {
            transition: opacity 0s ease;
            transition-delay: 0.3s;
        }
        @include media-to-desktop {
            display: none;
        }
    }
    
    .button-vi-text {
        transition: all .5s ease-in-out;
        transform: translateX(1rem);
        @include media-small-mobile {
            transform: none;
        }

        @include media-to-tablet {
            align-items: center;
        }

        @include media-to-desktop {
            position: relative;
            transform: none;
            transition: none;
            &:after {
                font-family: $font-IconSet;
                speak: none;
                font-style: normal;
                font-weight: 400;
                font-variant: normal;
                text-transform: none;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                @include transition(transform, .6s);
                display: inline-flex;
                margin-left: $margin-default/4;
                pointer-events: none;
                font-size: $size-regular;
                content: '\e901';
                margin-top: auto;
                margin-bottom: auto;
                @include rtl {
                    /*rtl:ignore*/
                    transform: translate(-6px, 0) rotate(-180deg);
                }
            }
            .svg-animation-vi {
                display: none;
            }
        }
    }
    @include hover {
        .button-vi-text {
            transform: translateX(-0.4rem);
            @include media-to-desktop {
                transform: none;
            }
        }
    }
}

@mixin button-vi-secondary {
    @include button-text-vi;
    padding: 1.5rem 4.6rem;
    background: transparent;
    z-index: 0;
    --svg-animate-duration-2nd-out: 0.6s;
    align-items: center;

    @include media-small-mobile {
        padding-top: 1.3rem;
        padding-bottom: 1.3rem;
    }
    .button-vi-text {
        transition: all .5s ease-in-out;
    }

    &::after {
        content: ' ';
        position: absolute;
        z-index: -1;
        left: 0;
        display: block;
        height: 100%;
        width: 0;
        background: $color-dark-coral;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;
        opacity: 0;
        transition: opacity 0s var(--svg-animate-duration-2nd-out);
        animation: secondary-button-animate-out var(--svg-animate-duration-2nd-out) ease forwards;
    }

    @include hover {
        &::after {
            opacity: 1;
            transition: opacity 0s 0s;
            animation: secondary-button-animate var(--svg-animate-duration-2nd-out) ease forwards;
        }        
    }
}

@mixin button-vi-secondary-small {
    padding: 1.2rem 3.6rem;
}

