@mixin btn-vi-primary-l1 {
    @include button-vi-primary;
    color: var(--button-vi-text-color, #{$color-white});
    @include media-small-mobile {
        width: 100%;
        .button-vi-text {
            transform: none;
            transition: none;
        }
        @include hover {
            color: var(--button-vi-text-hover-color, #{$color-white});
            .button-vi-text {
                transform: none;
            }
        } 
    }

    @include media-to-desktop {
        .button-vi-text {
            align-items: center;
        }
    }
}

@mixin btn-vi-primary-d1($text-color: $color-white) {
    @include button-vi-primary;
    border-color: $text-color;
    background: transparent;
    .button-vi-text, .svg-animation-vi {
        color: var(--button-vi-text-color, #{$text-color});
    }

    @include hover {
        @include media-to-desktop {
            border-color: $text-color;
            color: var(--button-vi-text-hover-color, #{$text-color});
            .button-vi-text {
                color: var(--button-vi-text-hover-color, #{$text-color});
            }
        }
    }
}

@mixin btn-vi-primary-d2 {
    @include button-vi-primary;
    border-color: $color-white;
    background: $color-white;
    .button-vi-text, .svg-animation-vi {
        color: var(--button-vi-text-color, #{$color-dark-coral});
    }
}

@mixin btn-vi-secondary-l1($background-color: transparent) {
    @include button-vi-secondary;
    overflow: hidden;
    background: $background-color;
    color: var(--button-vi-text-color, #{$color-dark-coral});
    @include hover {
        color: var(--button-vi-text-hover-color, #{$color-white});
    } 
}

// Default secondary button
@mixin btn-vi-secondary-l2($text-size: $size-regular, $text-line-height: 1, $text-color: $color-dark-coral, $hover-color: $color-dark-coral) {
    @include button-vi-secondary;
    border: 0;
    background: none;
    padding: 0 $padding-default/2 0 0;
    border-radius: 0;
    justify-content: start;
    text-decoration: none;

    &::after {
        content: none;
    }
    
    @include support-SVG-animation {
        .svg-animation-vi {
            transition: color .5s ease-in-out;
        }
    }

    .svg-animation-vi {
        transition: all .5s ease-in-out;
        transform: translateX(0);
        @include rtl {
            transform: translateX(0) scale(-1);
        }
    }

    .button-vi-text, .svg-animation-vi {
        color: var(--button-vi-text-color, #{$text-color});
        font-size: $text-size;
        line-height: $text-line-height;
    }

    .button-vi-text {
        display: inline;
    }

    @include hover {
        .button-vi-text, .svg-animation-vi {
            color: var(--button-vi-text-hover-color, #{$hover-color});
        }
        .svg-animation-vi {
            transform: translateX(0.8rem);
            transition-delay: .1s;
            @include rtl {
                transform: translateX(0.8rem) scale(-1);
            }
        }
        @include media-to-desktop {
            .button-vi-text {
                transform: none;
            }
        }
    }

    @include media-small-mobile {
        padding-left: 0;
        padding-right: 0;
    }

    @include media-to-desktop {
        .button-vi-text {
            transform: none;
        }
    }
}

@mixin btn-vi-secondary-d1($text-color:  $color-white, $hover-color: $color-core-purple) {
    @include button-vi-secondary;
    border-color: $text-color;
    background: transparent;
    color: var(--button-vi-text-color, #{$text-color});
    overflow: hidden;
    &::after {
        background: $text-color;
    }
    @include hover {
        color: var(--button-vi-text-hover-color, #{$hover-color});
    }
}

@mixin button-vi-external-link($text-color: $color-white, $background-color: $color-dark-coral) {
    @include button-text-vi;
    --external-link-icon-animation--dash2: external-link-dash2-out 0.35s 0.15s ease-in forwards;
    --external-link-icon-animation--arrow: external-link-arrow-out 0.35s ease-in-out forwards;
    --external-link-icon-animation--dash2-offset: 0;
    &:hover {
        --external-link-icon-animation--dash: external-link-dash 0.65s ease-in forwards;
        --external-link-icon-animation--dash2: external-link-dash2 0.25s 0.415s ease-out forwards;
        --external-link-icon-animation--arrow: external-link-arrow 0.9s ease-in-out forwards;
        --external-link-icon-animation--dash2-offset: 100;
        color: var(--button-vi-text-color, #{$text-color});
        text-decoration: none;
    }
    @include rtl {
        .svg-animation-vi {
            transform: scaleX(-1);
        }
        &:hover {
            --external-link-icon-animation--arrow: external-link-arrow-rtl 0.9s ease-in-out forwards;
        }
    }
    /* Safari fallback */
    @supports (hanging-punctuation: first) and (-webkit-appearance: none) {
        --external-link-icon-animation--dash: none;
        --external-link-icon-animation--dash2: none;
        --external-link-icon-animation--arrow: none;
        --external-link-icon-animation--dash2-offset: 0;
        &:hover {
            --external-link-icon-animation--dash: none;
            --external-link-icon-animation--dash2: none;
            --external-link-icon-animation--arrow: none;
            --external-link-icon-animation--dash2-offset: 0;
            svg{
                opacity: 1;
            }
        }

        .button-vi-text {
            transition: transform 0.5s ease;
            transition-delay: 0s;
        }

        .svg-animation-vi {
            opacity: 0;
            transition-delay: 0s;
            transition: all 0.5s ease;
        }       
    }
}

@mixin button-vi-external-link--static($text-color: $color-white, $background-color: $color-dark-coral) {
    @include button-vi-external-link;
    --external-link-icon-animation--dash2: none;
    --external-link-icon-animation--arrow: none;
    background: $background-color;
    color: var(--button-vi-text-color, #{$text-color});
    align-items: center;
    .button-vi-text {
        color: var(--button-vi-text-color, #{$text-color});
        margin-right: $margin-default/8;
    }
    &:hover {
        color: var(--button-vi-text-hover-color, #{$text-color});
        .button-vi-text {
            transform: none;
        }        
    }
    .svg-animation-vi {
        position: relative;
        transform: none;
        visibility: visible;
        width: var(--button-vi-external-link-icon-width);
        height: var(--button-vi-external-link-icon-height);
    }
    @supports (hanging-punctuation: first) and (-webkit-appearance: none) {
        .svg-animation-vi {
            opacity: 1;
        }
    }
}

@mixin button-vi-external-link-icon--only($icon-color: $color-dark-coral, $background-color: transparent, $icon-width: 1.4rem, $icon-height: 1.4rem) {
    @include button-vi-external-link;
    --button-vi-external-link-icon-color: #{$icon-color};
    --button-vi-external-link-icon-background-color: #{$background-color};
    --button-vi-external-link-icon-width: #{$icon-width};
    --button-vi-external-link-icon-height: #{$icon-height};
    --external-link-icon-animation--dash: none;
    --external-link-icon-animation--dash2: none;
    --external-link-icon-animation--arrow: none;
    padding: 0;
    background: var(--button-vi-external-link-icon-background-color);
    border: 0;
    color: var(--button-vi-external-link-icon-color);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include media-only-mobile {
        padding: 0;
    }
    &:hover {
        --external-link-icon-animation--dash: none;
        --external-link-icon-animation--dash2: none;
        --external-link-icon-animation--arrow: none;
        --external-link-icon-animation--dash2-offset: 0;
        .svg-animation-vi {       
            @include support-SVG-animation("not") {
                translate: unset;
            }
        }
    }
    .svg-animation-vi {       
        position: static;
        visibility: visible;
        transform: none;        
        width: var(--button-vi-external-link-icon-width);
        height: var(--button-vi-external-link-icon-height);
    }
    @supports (hanging-punctuation: first) and (-webkit-appearance: none) {
        .svg-animation-vi {
            opacity: 1;
        }
    }
}

@mixin link-tertiary-on-hover--hoverstate($hover-color: $color-dark-coral) {
    color: var(--button-vi-text-hover-color, #{$hover-color});
    text-decoration: none;
    @include media-from-desktop {
        animation: hyperlink-animate-in var(--hyperlink-animate-duration) linear forwards;
    }
}

@mixin link-tertiary-on-hover($text-color: $color-dark-coral, $hover-color: $color-dark-coral) {
    color: var(--button-vi-text-hover-color, #{$text-color});
    text-decoration: none;
    --hyperlink-animate-duration: .35s;
    --hyperlink-bg-color: #{$hover-color};
    display: inline;
    padding-bottom: 0.5rem;
    margin-bottom: -0.5rem;
    background-position-y: var(--link-tertiary--bg-position, 1.2em);
    @include media-from-desktop {
        overflow: hidden;
        background-repeat: no-repeat;
        background-image: linear-gradient(var(--hyperlink-bg-color), var(--hyperlink-bg-color));
        animation: hyperlink-animate-out var(--hyperlink-animate-duration) linear forwards;
    }

    @include hover {
        @include link-tertiary-on-hover--hoverstate($hover-color);
    }
}

@mixin taxonomy-button($theme: light-theme, $is-filter-button: false) {
    --tag-button-inactive-background: transparent;

    --tag-button-inactive-text-color: #{$color-dark-grey};

    --tag-button-active-background: #{$color-light-grey};
    --tag-button-active-text-color: #{$color-dark-purple};

    --tag-button-hovered-background: #{$color-dark-grey};
    --tag-button-hovered-text-color: #{$color-dark-purple};

    --tag-button-selected-background: #{$color-dark-purple};
    --tag-button-selected-text-color: #{$color-white};

    --tag-button-padding-right: #{$padding-default/2};
    @if($is-filter-button == true) {
        --tag-button-padding-right: #{$padding-default};
    }

    @if($theme == grey-theme){
        --tag-button-inactive-text-color: #{$color-dark-grey};

        --tag-button-active-background: #{$color-white};
        --tag-button-active-text-color: #{$color-dark-purple};

        --tag-button-hovered-background: #{$color-dark-grey};
        --tag-button-hovered-text-color: #{$color-dark-purple};

        --tag-button-selected-background: #{$color-dark-purple};
        --tag-button-selected-text-color: #{$color-white};
    } @else if($theme == purple-theme) {
        --tag-button-inactive-text-color: #{$color-light-purple};

        --tag-button-active-background: #{$color-light-purple};
        --tag-button-active-text-color: #{$color-dark-purple};

        --tag-button-hovered-background: #{$color-dark-purple};
        --tag-button-hovered-text-color: #{$color-white};

        --tag-button-selected-background: #{$color-white};
        --tag-button-selected-text-color: #{$color-dark-purple};
    } @else if($theme == dark-purple-theme) {
        --tag-button-inactive-text-color: #{$color-light-purple};

        --tag-button-active-background: #{$color-light-purple};
        --tag-button-active-text-color: #{$color-dark-purple};

        --tag-button-hovered-background: #{$color-core-purple};
        --tag-button-hovered-text-color: #{$color-white};

        --tag-button-selected-background: #{$color-white};
        --tag-button-selected-text-color: #{$color-core-purple};
    }
    @else if($theme == teal-theme) {
        --tag-button-inactive-text-color: #{$color-white};

        --tag-button-active-background: #{$color-light-grey};
        --tag-button-active-text-color: #{$color-dark-purple};

        --tag-button-hovered-background: #{$color-core-purple};
        --tag-button-hovered-text-color: #{$color-white};
        
        --tag-button-selected-background: #{$color-dark-purple};
        --tag-button-selected-text-color: #{$color-white};
    } @else if($theme == yellow-theme){
        --tag-button-inactive-text-color: #{$color-black};

        --tag-button-active-background: transparent;
        --tag-button-active-text-color: #{$color-black};

        --tag-button-hovered-background: #{$color-black};
        --tag-button-hovered-text-color: #{$color-white};
        
        --tag-button-selected-background: #{$color-black};
        --tag-button-selected-text-color: #{$color-white};
    } @else if($theme == bright-purple){
        --tag-button-active-background: #{$color-light-purple};
        --tag-button-inactive-text-color: #{$color-dark-purple};

        --tag-button-hovered-background: #{$color-dark-purple};
        --tag-button-hovered-text-color: #{$color-white};
    }

    font-size: $size-tiny;
    line-height: 2.2rem;
    padding: 0.1rem var(--tag-button-padding-right, $padding-default/2) 0.1rem $padding-default/2;
    border-radius: 2rem;
    color: var(--tag-button-active-text-color, $color-dark-grey);
    border: 1px solid transparent;
    background: var(--tag-button-active-background, $color-light-grey);
    text-decoration: none;
    display: inline-block;
    margin-top: $margin-default/2;
    @include transition;
    &:not(:last-of-type) {
        margin-right: $margin-default/2;
    }

    @include hover {
        color: var(--tag-button-hovered-text-color, $color-dark-purple);
        background: var(--tag-button-hovered-background, $color-dark-grey);
    }

    &.selected, &.active {
        color: var(--tag-button-selected-text-color, $color-white);
        background: var(--tag-button-selected-background, $color-dark-purple);
        @include media-from-lgtablet {
            &:hover {
                color: var(--tag-button-hovered-text-color, $color-dark-purple);
                background: var(--tag-button-hovered-background, $color-dark-grey);
            }

            &.focus {
                &:hover {
                    color: var(--tag-button-selected-text-color, $color-white);
                    background: var(--tag-button-selected-background, $color-dark-purple);
                }
            }

            &:focus-visible {
                outline-color: $color-dark-coral;
                outline-width: 1px;
            }
        }
    }

    &.in-active {
        color: var(--tag-button-inactive-text-color, $color-dark-grey);
        background: var(--tag-button-inactive-background, transparent);
        text-decoration: line-through;
        border-color: rgba(var(--tag-button-inactive-text-color, $color-dark-grey), 0.6);
        pointer-events: none;
        cursor: auto;
        @include hover {
            background: var(--tag-button-inactive-background, transparent);
            color: var(--tag-button-inactive-text-color, $color-dark-grey);
            border-color: rgba(var(--tag-button-inactive-text-color, $color-dark-grey), 0.6);
        }
    }
}

// All parameters are require.
@mixin step-forward-button($prevClass, $nextClass, $disabledClass, $currentClass) {
    border-radius: 100%;
    @include transition(transform, var(--article-animating-timer));
    width: $base-size;
    height: $base-size;
    overflow: hidden;
    border: 1.5px solid $color-light-purple;
    transform: none;
    margin-top: 0;
    margin-left: $margin-default/4;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: $color-light-purple;
    background: none;
    position: relative;
    left: 0;
    @include hover {
        text-decoration: none;
    }
    &::after {
        content: none;
    }
    @if $disabledClass != "" and $disabledClass != null {
        &.#{$disabledClass} {
            border-color: $color-mid-grey;
            color: $color-mid-grey;
            cursor: auto;
        }
    }
    @if $currentClass != "" and $currentClass != null {
        &.#{$currentClass} {
            background: $color-core-purple;
            border-color: $color-core-purple;
            color: $color-white;
        }
    }
    
    &:not(.#{$disabledClass}) {
        @include hover {
            transform: scale(1.25);
            &.#{$prevClass} {
                .arrow {
                    animation: glide-arrow-prev-button-1-animation .3s ease forwards;
                    animation-delay: .15s;
                }
                .arrow2 {
                    animation: glide-arrow-prev-button-2-animation .3s ease forwards;
                    animation-delay: .15s;
                }
            }
            &.#{$nextClass} {
                .arrow {
                    animation: glide-arrow-button-1-animation .3s ease forwards;                   
                    animation-delay: .15s;
                }
                .arrow2 {
                    animation: glide-arrow-button-2-animation .3s ease forwards;
                    animation-delay: .15s;
                }
            }
        }
    }

    svg {
        width: $base-size*3/8;
        height: 1rem;
    }

    &.#{$prevClass} {
        svg {
            transform: scaleX(-1);
        }
        .arrow2 {
            left: $base-size*1.25;
        }
    }

    @include rtl {
        transform: none;
        .arrow {
            transform: scaleX(-1) translateY(-50%);
        }
    }

    .arrow {
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -$base-size*0.1875;
        line-height: 2.2rem;
        height: 2.2rem;
        transform: translateY(-50%);
        @include transition(left, var(--article-animating-timer));
    }

    .arrow2 {
        left: -$base-size*0.1875;
    }
}

@mixin btn-quickview {
    text-decoration: none;
    color: $color-dark-purple;
    display: flex;
    align-items: center;
    border: 0;
    background: none;
    padding: 0;
    .quickview {
        @include media-only-mobile {
            font-size: $size-tiny;
        }
    }

    .social-icon__vi {
        margin-right: $margin-default/4;
