﻿@charset "UTF-8";

// Default Fonts Size Variables

$size-default: 1rem;
$size-x-gigantic: $size-default * 11.4;
$size-gigantic: $size-default * 8;
$size-huge: $size-default * 5.6;
$size-big: $size-default * 4.8;
$size-xxxx-large: $size-default * 4.2; 
$size-xxx-large: $size-default * 3.6;
$size-xx-large: $size-default * 3.2;
$size-x-large: $size-default * 2.4;
$size-very-large: $size-default * 2;
$size-large: $size-default * 1.8;
$size-regular: $size-default * 1.6;
$size-tiny: $size-default * 1.4;
$size-x-tiny: $size-default * 1.2;


//========= NEW TYPO's VARIABLES ========
$typoVersion: '' !default; // "" = current, V2 = new Typo

// DESKTOP
$size-hero-title: $size-default * 8.8;
$size-main-title: $size-default * 6;
$size-title: $size-big;
$size-subtitle: $size-default * 3.4;
$size-small-title: $size-x-large;

$lineheight-body-text-small: $size-default * 2.2;
$lineheight-body-text: $size-default * 2.4;
$lineheight-body-text-big: $size-default * 2.8;


// MOBILE
$size-hero-title-mobile: $size-big;
$size-main-title-mobile: $size-default * 4;
$size-title-mobile: $size-xx-large;
$size-subtitle-mobile: $size-x-large;
$size-small-title-mobile: $size-very-large;

$lineheight-body-text-small-mobile: $size-default * 2.2;
$lineheight-body-text-mobile: $size-default * 2.4;
$lineheight-body-text-big-mobile: $size-default * 2.4;
