/* ================================= 2 FONT SIZING ================================= */
@import "./typographyV2";

%font-medium {
    font-weight: $font-weight-medium;
}

%h1 {
    @extend %font-medium;
    font-size: $size-gigantic;
    line-height: 80px;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-big;
        line-height: 56px;
    }
}
%h2 {
    @extend %font-medium;
    font-size: $size-big;
    line-height: 1.1648em;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-xx-large;
        line-height: 40px;
    }
}
%h3 {
    @extend %font-medium;
    font-size: $size-x-large;
    line-height: 150%;
    margin: 0 0 15px 0;
}
%h4 {
    @extend %font-medium;
    font-size: $size-large;
    line-height: 150%;
    margin: 0 0 15px 0;
}
%p {
    font-size: $size-regular;
    line-height: 150%;
    margin: 0 0 15px 0;
}
%li {
    font-size: $size-regular;
    line-height: 150%;
}

%p-big {
    font-size: $size-very-large;
    line-height: 200%;
    font-weight: $font-weight-regular;
    margin: 0 0 15px 0;
}
%p-medium, %p-small {
    font-size: $size-large;
    line-height: 150%;
    font-weight: $font-weight-regular;
    margin: 0 0 15px 0;
}
%p-small {
    font-size: $size-tiny;
}
%p-tag {
    @include gt-body-text(small);
    letter-spacing: 0.2em;
    margin: 0 0 15px 0;
    a{
        color: inherit;
        text-decoration: none;
        @include hover{
            color: $color-dark-coral;
        }
    }
}
%richtext-block {
    h1 {
        @extend %h3;
    }
    p {
        line-height: 200%;
    }
}
%marketo-label {
    font-size: $size-regular;
    line-height: 150%;
    margin-bottom: 8px;
}