%btn {
    font-weight: $font-weight-regular;
    font-size: $size-regular;
    color: $color-white;
    background: $btn-primary-color;
    border: 2px solid $btn-primary-color;
    text-align: center;
    display: inline-block;
    padding: 1rem (3 * 1rem);
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    z-index: 1;
}

%btn-primart--small {
    padding: ($padding-default / 8) ($padding-default / 4);
    font-size: $size-x-tiny;
}

%btn-secondary-base {
    font-weight: $font-weight-regular;
    color: $btn-secondary-color;
    font-size: $size-regular;
    display: block;
    float: left;
    clear: left;
    background: none;
    border: none;
    text-align: left;
    text-decoration: none;
    position: relative;

    &:after {
        @extend %icon;
        @extend %icon-cta-arrow;
        @include transition(transform);
        position: absolute;
        top: 50%;
        position: absolute;
        pointer-events: none;
    }

    @include hover {
        text-decoration: none;
    }

    &[disabled] {
        opacity: .5;
        pointer-events: none;
    }
}

%btn-secondary {
    @extend %btn-secondary-base;
    padding: .5rem ($padding-default + 10px) .5rem 0;
    &:after {
        right: 0;
        transform: translate(-10px, -50%);
        @include rtl {
            /*rtl:ignore*/
            transform: translate(10px, -50%) rotate(-180deg);
        }
    }
    @include hover {
        &:after {
            transform: translate(0, -50%);
            @include rtl {
                /*rtl:ignore*/
                transform: translate(0, -50%) rotate(-180deg);
            }
        }
    }
}

%btn-secondary--reverse {
    @extend %btn-secondary-base;
    padding: .5rem 0 .5rem ($padding-default + 10px);
    &:after {
        left: 0;
        transform: translate(10px, -50%) rotate(-180deg);
        @include rtl {
            /*rtl:ignore*/
            transform: translate(-10px, -50%);
        }
    }
    @include hover {
        &:after {
            transform: translate(0, -50%) rotate(-180deg);
            @include rtl {
                /*rtl:ignore*/
                transform: translate(0, -50%);
            }
        }
    }
}


%more-container {
    margin-top: -25px;
    float: right;
}

%chips {
    display: inline-block;
    color: $color-dark-purple;
    padding: 0.75rem 3rem;
    font-size: $size-regular;
    text-decoration: none;
    border: 0.1rem solid $color-dark-grey;
    border-radius: 0.5rem;
    @include hover {
        color: $color-white;
        background-color: $color-dark-purple;
        border-color: $color-dark-purple;
    }
}

%chips-with-close {
    position: relative;
    padding-right: 4rem;
    padding-left: 1.5rem;
    &:before,
    &:after {
        content: '';
        position: absolute;
        height: 0.1rem;
        width: 1.5rem;
        background: $color-dark-purple;
        top: 2rem;
        right: 1rem;
    }
    &:after {
        transform: rotate(-45deg);
    }
    &:before {
        transform: rotate(45deg);
    }
    @include hover {
        &:before,
        &:after {
            background: $color-white;
        }
    }
}

%view-more-right-arrow {
    color: $color-dark-coral;
    font-size: $size-regular;
    padding-right: $padding-default;
    position: relative;
    display: inline-block;
    &:after {
        font-family: "GT-IconSet" !important;
        speak: none;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        border: none;
        outline: none;
        position: absolute;
        top: 50%;
        right: $base-size/4;
        font-size: 1.6rem;
        color: #de002e;
        transition: right 0.3s linear;
        margin-top: -$base-size/4;
        content: "\e901";
    }

    @include rtl {
        &:after {
            transform: rotate(180deg);
        }
    }

    &:hover {
        &:after {
            right: 0;
        }
    }
}

@mixin btn-close {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: var(--button-width-mobile, $base-size);
    height: var(--button-height-mobile, $base-size);
    background-color: var(--button-background-color, $color-light-purple);
    border-radius: 50%;
    border: none;
    cursor: pointer;
    text-decoration: none;
    @include transition;
    @include focus-outline($color-dark-purple);

    svg {
        width: var(--button-icon-width, $base-size*0.4375);
        height: var(--button-icon-height, $base-size*0.4375);
        color: var(--button-icon-color, $color-dark-purple);
        @include transition;
    }
    &:hover {
        background-color: var(--button-background-color-hover, $color-dark-purple);
        svg {
            color: var(--button-icon-color-hover, $color-white);
        }
    }
    @include media-from-tablet {
        width: var(--button-width, $base-size*1.5);
        height: var(--button-height, $base-size*1.5);
        svg {
            width: var(--button-icon-width-mobile, $base-size*0.75);
            height: var(--button-icon-height-mobile, $base-size*0.75);
        }
    }
}