@mixin subscribe-block-theme-styling {
    .subscribe-block__theme {
        margin: $margin-default 0;

        &.purple {
            background: $color-core-purple;

            .subscribe-contents__heading {
                .subscribe-contents__title {
                    color: $color-white;
                }
                h2.subscribe-contents__title {
                    &:not(.sectionTag):not(.exp):not(.title):not(.category):not(.fcb__title):not(.intro-title) {
                        color: $color-white;
                    }
                }

                .subscribe-content__style--button {
                    @include btn-vi-secondary-d1;
                }
            }
        }

        &.white {
            background: $color-white;
            border: 2px solid rgba($color-core-purple, 0.2);

            .subscribe-contents__style {
                padding: 0;
                margin: 0;

                @include media-from-tablet {
                    padding: 0 $padding-default/4;
                }

                .subscribe-icon__container {
                    width: $base-size + 18;
                    height: $base-size + 18;
                    border-width: 1px;
                    margin-right: $margin-default/2;
                    margin-top: $margin-default/4;

                    span.icon {
                        font-size: $size-x-large;
                        line-height: $base-size + 16;
                    }
                }

                .subscribe-content__style {
                    $icon-width: $base-size;
                    $border-icon: ($base-size / 16);

                    $space: ($base-size);
                    width: calc(100% - #{$icon-width} - #{$space} - #{$border-icon});

                    @include media-from-tablet {
                        $icon-width: $base-size + 18;
                        width: calc(100% - #{$base-size*3/4} - #{$icon-width} - #{$border-icon});
                    }
                }

                .insight-pdf__content {
                    display: inline-block;
                    vertical-align: top;

                    .download-pdf-link {
                        font-size: $size-very-large;
                        &:before {
                            content: none;
                        }
                    }
                }

                .subscribe-content__style--text {
                    margin-bottom: $margin-default/4;
                    font-size: $size-very-large;
                    line-height: $size-xx-large;
                    font-weight: $font-weight-regular;

                    span.heading {
                        font-weight: $font-weight-medium;

                        @include rtl {
                            font-weight: 600;
                        }
                    }
                }

                .subscribe-content__style--button {
                    font-size: $size-very-large;
                }
            }
        }

        .subscribe-contents__heading {
            text-align: left;

            .subscribe-contents__title {
                display: block;
                box-sizing: border-box;

                @include media-from-tablet {
                    display: inline-block;
                    vertical-align: middle;
                    max-width: 50%;
                }
            }

            .subscribe-contents__title {
                color: $color-black;
                font-weight: $font-weight-regular;
                font-size: $size-very-large;
                line-height: $size-xx-large;
                margin-bottom: $margin-default / 2;
                letter-spacing: -0.5px;

                @include media-from-tablet {
                    margin-right: $margin-default;
                    margin-bottom: 0;
                }
            }
            h2.subscribe-contents__title {
                &:not(.sectionTag):not(.exp):not(.title):not(.category):not(.fcb__title):not(.intro-title) {
                    font-size: $size-very-large;
                    line-height: $size-xx-large;
                    font-weight: $font-weight-regular;
                    margin-bottom: 0;
                    margin-right: $margin-default;
                }
            }

            .subscribe-content__style--button {
                @include btn-vi-secondary-l1($color-white);
                margin: 0;

                @include media-only-mobile {
                    padding-left: $padding-default / 2;
                    padding-right: $padding-default / 2;
                    margin-top: $base-size/2+4;
                }

                .icon {
                    line-height: $size-very-large;
                    font-size: $size-very-large;
                    position: static;
                    vertical-align: top;
                    display: table-cell;

                    @include media-only-mobile {
                        display: inline-block;
                        vertical-align: middle;
                    }

                    &.icon-bookmarks {
                        &:before {
                            content: "\e9d4";
                        }
                    }
                }

                span.button-text {
                    line-height: 2.2rem;
                    font-size: $size-regular;
                    text-align: left;
                    display: table-cell;
                    vertical-align: middle;
                    $space: $base-size*3/8;
                    padding-left: $margin-default / 4;

                    @include media-only-mobile {
                        display: inline-block;
                        vertical-align: middle;
                    }
                }
            }
        }

        .subscribe-icon__container {
            width: $base-size;
            height: $base-size;
            border: 2px solid $color-core-purple;
            margin-right: $margin-default / 2;
            display: inline-block;
            vertical-align: top;
            border-radius: 100%;
            text-align: center;
            margin-top: ($base-size - 2) / 6;

            @include media-from-tablet {
                margin-right: $margin-default;
                width: ($base-size - 2) * 3;
                height: ($base-size - 2) * 3;
                margin-top: 0;
            }

            span.icon {
                font-size: $size-regular;
                line-height: $base-size;
                color: $color-core-purple;

                @include media-from-tablet {
                    font-size: $size-big;
                    line-height: ($base-size - 2) * 3;
                }
            }
        }

        .subscribe-contents__style {
            margin-top: $margin-default;
            font-size: 0;
        }

        .subscribe-content__style {
            display: inline-block;
            vertical-align: top;
            $icon-width: $base-size;
            $border-icon: ($base-size / 8);

            $space: ($base-size / 2);
            width: calc(100% - #{$icon-width} - #{$space} - #{$border-icon});

            @include media-from-tablet {
                $icon-width: ($base-size - 2) * 3;
                width: calc(100% - #{$base-size} - #{$icon-width} - #{$border-icon});
            }

            .subscribe-content__style--subtitle {
                font-style: oblique;
                margin-bottom: $base-size/4;
                font-size: $size-very-large;
                line-height: $size-xx-large;

                @include rtl {
                    font-weight: 600;
                }
            }

            .subscribe-content__style--list {
                li {
                    font-size: $size-very-large;
                    line-height: $size-x-large;
                    color: $color-black;
                    font-weight: $font-weight-regular;
                    font-style: oblique;
                    padding-left: $padding-default / 2;

                    span {
                        display: inline-block;
                        vertical-align: middle;
                        font-size: $size-very-large;
                        line-height: $size-xx-large;
                        color: $color-black;
                        font-weight: $font-weight-regular;
                        font-style: oblique;
                        word-break: break-word;
                    }

                    &:before {
                        content: "";
                        height: ($base-size - 2) / 5;
                        width: ($base-size - 2) / 5;
                        border-radius: 100%;
                        background: $color-black;
                        display: inline-block;
                        margin-right: ($base-size - 2) / 5;
                        margin-top: ($base-size) / 8;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}
