@mixin transition($prop: all, $duration: .3s, $delay: 0s, $easing: cubic-bezier(0.4, 0.0, 0.2, 1)){
	transition: $prop $duration $delay $easing;
}

@mixin transition-mix($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  transition: $args;
}

@mixin nav3-transition {
  transition: all 0.3s ease;
}