//
// Grid system
// --------------------------------------------------

@mixin container {
  @include container-fixed;
  .dynamic-container,
  .container{
    width: auto;
    padding: 0;
    .row{
      margin-left: 0;
      margin-right: 0;
    }
  }
  .dynamic-container {
    max-width: 100%;
  }
  [class*="col-"]{
    .container{
      .row{
        margin-left: ($grid-gutter-width * -0.5);
        margin-right: ($grid-gutter-width * -0.5);
      }
    }
  }
  @media (max-width: $screen-sm-min) {
    width: 100%;
  }
  @media (min-width: $screen-sm-min) {
    width: $container-sm;
  }
  @media (min-width: $screen-md-min) {
    width: $container-md;
  }
  @media (min-width: $screen-lg-min) {
    width: $container-lg;
  }
}

@mixin dynamic-container {
  @include container-fixed;
  .dynamic-container,
  .container{
    width: auto;
    padding: 0;
    .row{
      margin-left: 0;
      margin-right: 0;
    }
  }
  [class*="col-"]{
    .container{
      .row{
        margin-left: ($grid-gutter-width * -0.5);
        margin-right: ($grid-gutter-width * -0.5);
      }
    }
  }
  @media (max-width: $screen-sm-min) {
    width: 100%;
  }
  @media (min-width: $screen-sm-min) {
    max-width: $container-sm;
  }
  @media (min-width: $screen-md-min) {
    max-width: $container-md;
  }
  @media (min-width: $screen-lg-min) {
    max-width: $container-lg;
  }
}


@mixin flexible-container($has-elevator: true) {
  @include media-from-tablet {
    width: calc(100% - #{$base-size*3});
  }
  @include media-from-lgtablet {
      @include border-radius("other");
      // width: calc(100% - #{$base-size});
      @if $has-elevator == true {
        @include elevator("default", $base-size/8, $base-size/4, rgba($color-dark-purple, 0.16));
      }
  }

  @include media-from-desktop-md {
      width: calc(100% - #{$base-size*2});
  }
  @include media-from-desktop-lg {
      width: calc(114rem + #{$base-size*4});
      max-width: calc(100% - #{$base-size*2});
  }
  @include media-from-desktop-lg-extra {
      width: calc(114rem + #{$base-size*5});
  }
}




@mixin flexible-container-v2($has-elevator: true) {
  @include media-from-lgtablet {
      // width: calc(100% - #{$base-size});
      @if $has-elevator == true {
        @include elevator("default", $base-size/8, $base-size/4, rgba($color-dark-purple, 0.16));
        @include border-radius("other");
      }
      width: calc(100% - #{$base-size});
      & > .container {
        width: 100%;
      }
  }

  @include media-from-desktop {
    & > .container {
        width: 100%;
    }
  }

  @include media-from-desktop-lg {
      width: calc(114rem + #{$base-size*4});
      & > .container {
        width: 1172px;
      }
  }
  @include media-from-desktop-lg-extra {
      width: calc(114rem + #{$base-size*5});
  }
}