@function is-direction($value) {
  $is-keyword: index((to top, to top right, to right top, to right, to bottom right, to right bottom, to bottom, to bottom left, to left bottom, to left, to left top, to top left), $value);
  $is-angle: type-of($value) == 'number' and index('deg' 'grad' 'turn' 'rad', unit($value));

  @return $is-keyword or $is-angle;
}

@function legacy-direction($value) {
  @if is-direction($value) == false {
    @error "Cannot convert `#{$value}` to legacy syntax because it doesn't seem to be a direction.";
  }

  $conversion-map: (
    to top          : bottom,
    to top right    : bottom left,
    to right top    : left bottom,
    to right        : left,
    to bottom right : top left,
    to right bottom : left top,
    to bottom       : top,
    to bottom left  : top right,
    to left bottom  : right top,
    to left         : right,
    to left top     : right bottom,
    to top left     : bottom right
  );

  @if map-has-key($conversion-map, $value) {
    @return map-get($conversion-map, $value);
  }

  @return 90deg - $value;
}

@mixin linear-gradient($direction, $color-stops...) {
  // Direction has been omitted and happens to be a color-stop
  @if is-direction($direction) == false {
    $color-stops: $direction, $color-stops;
    $direction: 180deg;
  }

  background: nth(nth($color-stops, 1), 1);
  background: -webkit-linear-gradient(legacy-direction($direction), $color-stops);
  background: linear-gradient($direction, $color-stops);
}

@mixin image-gradient($image-gradient-color-start: top, 
                      $image-gradient-color: $color-black, 
                      $image-gradient-opacity: 0.6) {
        
    &:after {
        content: '';
        position: absolute;
        top: 0;
        right: -999em;
        bottom: 0;
        left: -999em;
        background-image: linear-gradient(to #{$image-gradient-color-start}, transparent, #{$image-gradient-color});
        opacity: $image-gradient-opacity; 
    }
}

@mixin svg-graphic-texture {
    .svg-graphic-texture {
      width: auto;
      height: 100%;
      position: absolute;
      right: 5%;
      transform: var(--svg-graphic-texture-transform, translate3d(0px, 0px, 0px));
      opacity: 0.6;
      aspect-ratio: 16/9;
      color: $color-white;

      @include media-to-lgtablet {
          display: none;
      }

      path {
          fill-opacity: 0;
      }

      &.var1,
      &.var2,
      &.var3,
      &.var4 {
          .animation-item {
              animation-timing-function: cubic-bezier(0.49, 0.24, 0.38, 0.97);
              animation-delay: 0.1s;
          }
      }

      &.var1 {
          .svg-graphic-texture-path1 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1011.3800048828125,22.8799991607666);

              g {
                  animation-delay: 0.4s;
                  --element-fade-in-y: 150%;
              }
          }

          .svg-graphic-texture-path2 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1293.428955078125,280.6789855957031);

              g {
                  animation-delay: 0.3s;
                  --element-fade-in-y: 100%;
              }
          }

          .svg-graphic-texture-path3 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1202.3489990234375,280.6730041503906);

              g {
                  animation-delay: 0.2s;
                  --element-fade-in-y: 50%;
              }
          }
      }

      &.var2 {
          .svg-graphic-texture-path1 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1193.3800048828125,268.8800048828125);

              g {
                  --element-fade-in-y: 150%;
              }
          }

          .svg-graphic-texture-path2 {
              /*rtl:ignore*/
              transform: matrix(1.1318199634552002,0,0,1.1318199634552002,1203.428955078125,334.67901611328125);

              g {
                  --element-fade-in-y: 80%;
              }
          }

          .svg-graphic-texture-path3 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1096.3489990234375,280.6730041503906);

              g {
                  --element-fade-in-y: 75%;
              }
          }
      }

      &.var3 {
          .svg-graphic-texture-path1 {
              /*rtl:ignore*/
              transform: matrix(1, 0, 0, 1, 1439.38, 225.88) scale(0.75);

              path {
                  stroke-width: 3px;
              }

              g {
                  --element-fade-in-y: 150%;
              }
          }

          .svg-graphic-texture-path2 {
              /*rtl:ignore*/
              transform: matrix(1, 0, 0, 1, 1303.42896, 352.67899);

              g {
                  --element-fade-in-y: 50%;
              }
          }

          .svg-graphic-texture-path3 {
              /*rtl:ignore*/
              transform: matrix(1, 0, 0, 1, 1323.349, 260.673) scale(0.8);

              path {
                  stroke-width: 3px;
                  transform: rotate(-13deg);
              }

              g {
                  --element-fade-in-y: 80%;
              }
          }
      }

      &.var4 {
          scale: 1.1;
          translate: 0 2%;

          .svg-graphic-texture-path1 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1273.380005, 367.120001) scale(1.18);

              g {
                  --element-fade-in-y: 80%;
              }
          }

          .svg-graphic-texture-path2 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,1143.428955,275.678986);

              path {
                  transform: scale(0.91) rotate(7deg);
                  stroke-width: 3px;
              }

              g {
                  --element-fade-in-y: 50%;
              }
          }

          .svg-graphic-texture-path3 {
              /*rtl:ignore*/
              transform: matrix(1,0,0,1,900.348999,262.673004); #{"/*rtl:ignore*/"}

              path {
                  stroke-width: 3px;
              }

              g {
                  --element-fade-in-y: 80%;
              }
          }
      }
  }

}


@mixin static-gradient-background {
    &.gradient-primary__bright {
        background: linear-gradient(135deg, $color-core-purple 0%, $color-light-purple 100%);
    }

    &.gradient-secondary__coral {
        background: linear-gradient(135deg, $color-core-purple 0%, $color-coral 100%);
    }

    &.gradient-primary__dark {
        background: linear-gradient(135deg, $color-dark-purple 0%, $color-core-purple 100%);
    }

    &.gradient-secondary__teal  {
        background: linear-gradient(135deg, $color-core-purple 0%, $color-teal 100%);
    }
}