﻿@charset "UTF-8";

// Default Fonts Variables

$font-Regular: "GT-Walsheim-Pro", Arial, Helvetica, sans-serif;
$font-IconSet: "GT-IconSet";

/*font family variables for localisation*/
$serif: Georgia, Times, serif;
$sanSerif: Arial,Helvetica,sans-serif;
$sanSerifNarrow: "Arial Narrow", Arial,Helvetica,sans-serif;
// $gtWalsheimJap: "GT-Walsheim-Pro", -apple-system, BlinkMacSystemFont, ".SFNSDisplay-Regular", "Yu Gothic", YuGothic, "メイリオ", Meiryo, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", Arial, Helvetica, Sans-Serif;
// $sanSerifJap: Arial, Roboto, "Droid Sans", "游ゴシック", YuGothic, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
// $sanSerifJapRichtext: "游ゴシック", YuGothic, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, Arial, Roboto, "Droid Sans", sans-serif;
$KORFontFamily: 'Nanum Gothic', sans-serif;
$JPNFontFamily: "Meiryo UI", Meiryo, sans-serif;
$sanSerifJap: $JPNFontFamily;
$sanSerifJapRichtext: $JPNFontFamily;
$malgun: 'Malgun Gothic', sans-serif;
$newGothic: "news gothic", Arial, sans-serif;
$times: Times, serif;

// REVERT FONT WEIGHT LOGIC FOR LOCALIZATION FONTS
// USING 400 for Regular
// USING 300 for Medium
// USING 200 for Bold
// I.e. GT Bold = Arial 200, and Arial doesn't have 200 weight. Hence browser will use Arial 200.
// REFER _localisations.scss
// ==================================

$font-weight-regular: var(--font-weight-normal);
$font-weight-medium: var(--font-weight-semibold);
$font-weight-bold: var(--font-weight-bold);

// GT Icon font bold
$icon-weight-regular: 400;
$icon-weight-bold: 700;

// Link underline
$underline-offset: 0.18em;


$h2-with-not-selector: "h2:not(.sectionTag):not(.exp):not(.title):not(.category):not(.fcb__title):not(.intro-title)";





$tinymce-selectors: "p, li, span, pre, blockquote, legend, td, th";
$tinymce-selectors-compact: "p, li, pre, blockquote, legend";
$tinymce-selectors-full: "p, li, span, pre, blockquote, legend, td, th, div";