@mixin typography {
    /* ================================= 2 FONT SIZING ================================= */
    // html {
    //     font-size: 62.5%;
    //     -webkit-font-smoothing: antialiased;
    //     -ms-text-size-adjust: 100%; /* 2 */
    //     -webkit-text-size-adjust: 100%; /* 2 */
    // }
    // @media only screen and( -webkit-min-device-pixel-ratio: 1.25 ),
    //     only screen and(      -o-min-device-pixel-ratio: 5/4 ),
    //     only screen and( min-resolution: 120dpi ),
    //     only screen and( min-resolution: 1.25dppx ) {
    //     body {
    //         font-size: $size-default;
    //     }
    // }
    // body {
    //     font-size: $size-default;
    //     font-family: $font-Regular;
    //     line-height: 1.4;
    //     font-weight: $font-weight-regular;
    // }
    /**
    * Address `font-family` inconsistency between `textarea` and other form
    * elements.
    */

    html,
    button,
    input,
    select,
    textarea {
        font-family: inherit;
    }

    input, select, textarea {
        font-weight: $font-weight-regular;
    }

    p {
        font-size: $size-regular;
        line-height: 150%;
        margin: 0 0 15px 0;
    }

    .gt-wrapper {
        @extend %custom-paragraph;
    }

    li {
        font-size: $size-regular;
        line-height: 150%;
    }

    a {
        .sectionTag {
            color: inherit;
        }
    }

    a {
        text-decoration: none;
        color: $color-core-purple;

        @include hover {
            color: $color-dark-coral;
            @include link-underline;
        }

        @include focus-outline;
    }
    /**
    * Improve readability when focused and also mouse hovered in all browsers.
    */

    .link-icon {
        display: flex;
        margin-bottom: 6px;

        .icon {
            font-size: $size-x-large;
            line-height: 2.4rem;
            vertical-align: middle;
            margin-right: 10px;
            color: $color-core-purple;
            @include map-links-icon();
        }

        a {
            font-size: $size-regular;
        }
    }
    /**
    * Address margins set differently in IE 6/7.
    */

    p,
    pre {
        margin: 1em 0;
    }

    h1, .h1 {
        @extend %font-medium;
        font-size: $size-gigantic;
        line-height: 80px;
        margin: 0 0 15px 0;

        @include media-only-mobile {
            font-size: $size-big;
            line-height: 56px;
        }
    }

    h2, .h2 {
        @include gt-subtitle(true, "semibold");
        margin: 0 0 15px 0;
    }

    h3, .h3, .big-text {
        @extend %font-medium;
        font-size: $size-x-large;
        line-height: 150%;
        margin: 0 0 15px 0;
    }

    .tinymce-wrapper {
        h2 {
            @include gt-subtitle(true, "semibold");
        }

        h3, .h3, .big-text {
            @include gt-small-title(true, "semibold");
        }

        h4, .h4, .medium-text {
            @include gt-body-text("big", "semibold");
        }

        @include anchor-scroll-offset;
    }

    h4, .h4, .medium-text {
        @extend %font-medium;
        font-size: $size-large;
        line-height: 150%;
        margin: 0 0 15px 0;
    }

    .auto-soft-hyphens {
        @include soft-hyphens;
    }
}
