// ==================================
//        STYLES
// ==================================
%title--medium {
    @extend %h3;
    display: block;
    font-size: $size-large;
    color: $color-core-purple;
}

%title--underlined {
    padding-bottom: ($padding-default * 0.25);
    border-bottom: 1px solid $color-dark-grey;
}

%category-heading {
    margin: -25px 0 $margin-default;
    word-wrap: break-word;
    overflow-wrap: break-word;
    
    &--flexible {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        margin: -25px 0 0;
    }
    &__heading {
        width: 100%;
        margin-bottom: 0;
        word-wrap: break-word;
        overflow-wrap: break-word;
        margin-bottom: $margin-default - $margin-default / 4;
    }
    &__more {
        margin-top: $margin-default / 4;
        padding-top: 0;
        border-bottom: 2px solid $color-dark-coral;
        margin-left: auto;
        &:after {
            margin-top: -3px;
        }
    }
    &__heading,
    &__more {
        justify-self: flex-end;
    }
    @include media-from-tablet {
        &__heading {
            margin-right: $margin-default;
        }
        &__heading,
        &__more {
            width: auto;
        }
    }
}