@import "media";

@mixin hover {
    @include media-touch-devices {
        outline: none;
    }
    @include media-nontouch-devices {
        /*outline: none;*/
        cursor: pointer;
        &:hover, &:active, &:focus-visible {
            @content;
        }
    }
}

@mixin hoverWithNoFocus {
    @include media-touch-devices {
        outline: none;
    }
    @include media-nontouch-devices {
        /*outline: none;*/
        cursor: pointer;
        &:hover, &:active, &:focus-visible:not(.no-focus) {
            @content;
        }
    }
}

@mixin outline($color: "") {
    @include media-touch-devices {
        outline: none;
    }
    @include media-nontouch-devices {
        outline: thin dotted #{$color};
        outline-offset: -1px;
    }
}

@mixin focus-outline($color: "") {
    &:focus-visible {
        @include outline($color);
    }
}

@mixin focus() {
    &:focus-visible {
        @include outline($color);
    }
}