@function getFontSize($type, $sub-type: "") {
    $font-size: "";
    $line-height: "";
    $font-size-mobile: "";
    $line-height-mobile: "";

    @if $type == "gt-hero-title" {
        $font-size: $size-hero-title;
        $line-height: 8.8rem;
        $font-size-mobile: $size-hero-title-mobile;
        $line-height-mobile: 5.6rem;
    }

    @if $type == "gt-main-title" {
        $font-size: $size-main-title;
        $line-height: 6.6rem;
        $font-size-mobile: $size-main-title-mobile;
        $line-height-mobile: 4.8rem;
    }

    @if $type == "gt-title" {
        $font-size: $size-title;
        $line-height: 5.6rem;
        $font-size-mobile: $size-title-mobile;
        $line-height-mobile: 4rem;
    }

    @if $type == "gt-subtitle" {
        $font-size: $size-subtitle;
        $line-height: 4rem;
        $font-size-mobile: $size-subtitle-mobile;
        $line-height-mobile: 3.2rem;
    }

    @if $type == "gt-small-title" {
        $font-size: $size-small-title;
        $line-height: 3.2rem;
        $font-size-mobile: $size-small-title-mobile;
        $line-height-mobile: 2.8rem;
    }

    @if $type == "gt-body-text" {
        $font-size: $size-regular;
        $line-height: $lineheight-body-text;
        $font-size-mobile: $size-regular;
        $line-height-mobile: $lineheight-body-text-mobile;
    }

    @if $type == "old-tinymce" {
        $font-size: $size-large;
        $line-height: 2.7rem;
    }

    @if $type == "gt-body-text" and $sub-type == "small" {
        $font-size: $size-tiny;
        $line-height: $lineheight-body-text-small;
        $font-size-mobile: $size-tiny;
        $line-height-mobile: $lineheight-body-text-small-mobile;
    }

    @if $type == "gt-body-text" and $sub-type == "big" {
        $font-size: $size-very-large;
        $line-height: $lineheight-body-text-big;
        $font-size-mobile: $size-large;
        $line-height-mobile: $lineheight-body-text-big-mobile;
    }

    @if $type == "gt-body-text" and $sub-type == "tiny" {
        $font-size: $size-x-tiny;
        $line-height: $size-regular;
    }

    @return (
        font-size: $font-size,
        line-height: $line-height,
        font-size-mobile: $font-size-mobile,
        line-height-mobile: $line-height-mobile
    );
}


@mixin gt-font-weight($type: "", $prefix: "", $type-mobile: "") {
    $default-type: var(--font-weight-normal);
    @if $type == "black" {
        $default-type: var(--font-weight-black);
    }
    @if $type == "bold" {
        $default-type: var(--font-weight-bold);
    }
    @if $type == "semibold" {
        $default-type: var(--font-weight-semibold);
    }
    @if $type == "light" {
        $default-type: var(--font-weight-light);
    }

    @if $type-mobile != "" {
        @include media-from-desktop-md {
            @if $prefix != "" {
                font-weight: var(--#{$prefix}--font-weight, #{$default-type});
            }
            @else {
                font-weight: #{$default-type};
            }
        }
        @include media-to-desktop-md {
            @if $prefix != "" {
                font-weight: var(--#{$prefix}--font-weight, #{$type-mobile});
            }
            @else {
                font-weight: #{$type-mobile};
            }
        }
    } @else {
        @if $prefix != "" {
            font-weight: var(--#{$prefix}--font-weight, #{$default-type});
        }
        @else {
            font-weight: #{$default-type};
        }
    }
}

/**
// DESKTOP
$size-hero-title: $size-default * 8.8; //8rem
$size-main-title: $size-default * 6; //6rem
$size-title: $size-big; //4.8rem
$size-subtitle: $size-default * 3.4; //3.4rem
$size-small-title: $size-x-large; //2.4rem

// MOBILE
$size-hero-title-mobile: $size-big; //4.8rem
$size-main-title-mobile: $size-default * 4; // 4rem
$size-title-mobile: $size-xx-large; // 3.2rem
$size-subtitle-mobile: $size-x-large; //2.4rem
$size-small-title-mobile: $size-very-large; // 2rem
*/
@mixin generateTypo($prefix, $font-size, $font-size-mobile, $line-height, $line-height-mobile) {

    @if $prefix != "" {
        @if $font-size-mobile != "" {
            @include media-from-desktop-md {
                font-size: var(--#{$prefix}--font-size, #{$font-size});
                line-height: var(--#{$prefix}--line-height, #{$line-height});
            }
            @include media-to-desktop-md {
                font-size: var(--#{$prefix}--font-size-mobile, #{$font-size-mobile});
                line-height: var(--#{$prefix}--line-height-mobile, #{$line-height-mobile});
            }
        } @else {
            font-size: var(--#{$prefix}--font-size, #{$font-size});
            line-height: var(--#{$prefix}--line-height, #{$line-height});
        }
    } @else {
        @if $font-size-mobile != "" {
            @include media-from-desktop-md {
                font-size: #{$font-size};
                line-height: #{$line-height};
            }
            @include media-to-desktop-md {
                font-size: #{$font-size-mobile};
                line-height: #{$line-height-mobile};
            }
        } @else {
            font-size: #{$font-size};
            line-height: #{$line-height};
        }
    }
}

@mixin gt-hero-title($has-size-mobile: true, $font-weight: "bold") {
    $size-mobile: "";
    @if $has-size-mobile {
        $size-mobile: $size-hero-title-mobile;
    }
    @include generateTypo("gt-hero-title", $size-hero-title, $size-mobile, 8.8rem, 5.6rem);
    @include gt-font-weight($font-weight, "gt-hero-title");
}

@mixin gt-main-title($has-size-mobile: true, $font-weight: "bold") {
    $size-mobile: "";
    @if $has-size-mobile {
        $size-mobile: $size-main-title-mobile;
    }
    @include generateTypo("gt-main-title", $size-main-title, $size-mobile, 6.6rem, 4.8rem);
    @include gt-font-weight($font-weight, "gt-main-title");
}

/**
 * @param $type: "gt-main-title"
 * @param $font-weight: "black"
 */
@mixin gt-flexible-banner-title($type, $font-weight: "black") {
    $data: getFontSize($type);

    @include media-from-desktop-lg {
        font-size: var(--#{$type}--font-size, map-get($data, "font-size"));
        line-height: var(--#{$type}--line-height, map-get($data, "line-height"));
    }

    @include media-to-desktop-lg {
        font-size: var(--#{$type}--font-size-mobile, map-get($data, "font-size-mobile"));
        line-height: var(--#{$type}--line-height-mobile, map-get($data, "line-height-mobile"));
    }
    @include gt-font-weight($font-weight, $type);
}


@mixin gt-title($has-size-mobile: true, $font-weight: "light") {
    $size-mobile: "";
    @if $has-size-mobile {
        $size-mobile: $size-title-mobile;
    }
    @include generateTypo("gt-title", $size-title, $size-mobile, 5.6rem, 4rem);
    @include gt-font-weight($font-weight, "gt-title");
}

@mixin gt-subtitle($has-size-mobile: true, $font-weight: "light") {
    $size-mobile: "";
    @if $has-size-mobile {
        $size-mobile: $size-subtitle-mobile;
    }
    @include generateTypo("gt-subtitle", $size-subtitle, $size-mobile, 4rem, 3.2rem);
    @include gt-font-weight($font-weight, "gt-subtitle");
}

@mixin gt-small-title($has-size-mobile: true, $font-weight: "light") {
    $size-mobile: "";
    @if $has-size-mobile {
        $size-mobile: $size-small-title-mobile;
    }
    @include generateTypo("gt-small-title", $size-small-title, $size-mobile, 3.2rem, 2.8rem);
    @include gt-font-weight($font-weight, "gt-small-title");
}

@mixin gt-body-text($type: "", $font-weight: "") {
    $size-mobile: "";
    $size-desktop: $size-regular;
    $line-height: $lineheight-body-text;
    $line-height-mobile: $lineheight-body-text-mobile;

    @if $type == "small" {
        $size-desktop: $size-tiny;
        $line-height: $lineheight-body-text-small;
        $line-height-mobile: $lineheight-body-text-small-mobile;
    }

    @if $type == "old-tinymce" {
        $size-desktop: $size-large;
        $line-height: 2.7rem;
    }

    @if $type == "big" {
        $size-desktop: $size-very-large;
        $size-mobile: $size-large;
        $line-height: $lineheight-body-text-big;
        $line-height-mobile: $lineheight-body-text-big-mobile;

        @if $font-weight =="" {
            $font-weight: "light";
        }
    }

    @if $type == "tiny" {
        $size-desktop: $size-x-tiny;
        $line-height: $size-regular;
        $line-height-mobile: $size-regular;
    }

    @if $font-weight == "" {
        $font-weight: "regular";
    }

    @include generateTypo("gt-body-text", $size-desktop, $size-mobile, $line-height, $line-height-mobile);
    @include gt-font-weight($font-weight, "gt-body-text");
}

@mixin soft-hyphens {
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
}

@mixin get-in-touch {
    text-transform: uppercase;
    letter-spacing: 2.4px;
    @include gt-body-text; //override tinyMCE's font-size
}

@mixin tinymce-line-length {
    .line-length {
        .tinymce-wrapper {
            ul, ol,
            p {
                max-width: $line-length;
            }
        }
    }
}


//@include set-font-size("gt-small-title", "mobile", "gt-body-text");
@mixin set-font-size($type, $screen: "", $new-type: "", $new-sub-type: "") {
    $data: getFontSize($new-type, $new-sub-type);

    @if($screen == "") {
        --#{$type}--font-size: #{map-get($data, "font-size")};
        --#{$type}--line-height: #{map-get($data, "line-height")};
    }

    @if($screen == "mobile") {
        --#{$type}--font-size-mobile: #{map-get($data, "font-size-mobile")};
        --#{$type}--line-height-mobile: #{map-get($data, "line-height-mobile")};
    }
}
