%box-base-white {
    position: relative;
    margin-top: ($margin-default * 0.75);
    margin-bottom: $margin-default;
    padding: ($margin-default * 0.75) ($margin-default * 0.75) $margin-default;
    box-sizing: border-box;
    background-color: $color-white;
    background-position: right bottom;
    background-repeat: no-repeat;
    a {
        display: inline-flex;
        max-width: 100%;
        text-overflow: ellipsis;
        text-decoration: none;
        vertical-align: bottom;
        width: auto;
    }
    .expert {
        @extend %h3;
    }
    &.solid-b {
        background-color: $color-dark-purple;
        color: $color-white;
        a,
        h2,
        .sectionTag,
        .category {
            color: inherit;
        }
        a {
            @include hover {
                @include link-underline;
            }
        }
    }
    &.solid-b--purple {
        background-color: $color-core-purple;
        color: $color-white;
        a,
        h2,
        .sectionTag,
        .category {
            color: inherit;
        }
        a {
            @include hover {
                @include link-underline;
            }
        }
    }
	&.contact-block{
		.email-obfuscate{
			@include rtl {
				/*rtl:ignore*/
				direction: ltr;
			}
		}
	}
}

%sml-cont,
%med-cont,
%lrg-cont {
    @extend %box-base-white;
    max-height: $height-promoblocks;
    margin: 0 0 $margin-default 0;
    overflow-wrap: break-word;
    word-wrap: break-word;
}

%sml-cont {
    min-height: $height-promoblock-sml;
    .withSecondaryNav & {
        min-height: ($height-promoblock-sml - $base-size);
    }
}

%med-cont,
%lrg-cont {
    min-height: $height-promoblocks;
    .withSecondaryNav & {
        min-height: ($height-promoblocks - ($base-size * 2));
    }
}

%filter-bar {
    @extend %clear;
    margin-top: 0;
    margin-bottom: 0;
    background: $color-core-purple;
    color: $color-white;

    h2.title {
        display: -ms-flexbox;
        display: flex;
        flex-shrink: 0;
        font-size: $size-x-large;
        line-height: 2.2em;
        vertical-align: top;
        margin: ($margin-default * .75) $margin-default 0 0;

        @include media-only-mobile {
            width: 100%;
        }

        @include rtl {
            margin: ($margin-default * .75) $margin-default 0 0;
        }
    }

    @include media-from-tablet {
        input,
        button {
            margin: 0;
        }
    }

    [class*="col"] {
        @include media-only-mobile {
            div,
            button {
                display: block;
            }

            .form-select select {
                width: 100%;
            }

            input {
                margin-bottom: ($margin-default / 2);
            }
        }

        @include media-from-tablet {
            button,
            a {
                margin: 0;
            }
        }
    }

    div.define-filter {
        display: -ms-flexbox;
        display: flex;
        flex-grow: 1;
        margin-top: ($margin-default * .75);
        margin-bottom: ($margin-default * .75);

        button {
            flex-shrink: 0;
        }

        @include media-only-mobile {
            flex-wrap: wrap;
            flex-direction: column;
            flex-grow: 0;
            width: 32rem;
            margin-bottom: ($margin-default / 2);

            button {
                background: $color-white;
                color: $color-dark-purple;

                @include hover {
                    color: $color-white;

                    &:after {
                        background-color: $color-core-purple;
                    }

                    @include pointer-coarse {
                        color: $color-dark-purple;

                        &:after {
                            display: none;
                        }
                    }
                }
            }
        }

        @include media-small-mobile {
            flex-grow: 1;
            width: 100%;
        }
    }

    div.define-search {
        display: -ms-flexbox;
        display: flex;
        align-items: flex-start;

        @include media-only-mobile {
            flex-wrap: wrap;
        }

        .js-define-search {
            display: inline-block;
            display: -ms-flexbox;
            display: flex;
            flex-grow: 1;
            align-items: flex-end;
            padding-top: 0;
            padding-left: 0;
            margin: 0;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin-bottom: -.1rem;

            li {
                display: inline-block;
                margin-top: 4rem;
                margin-bottom: 4rem;

                a {
                    position: relative;
                    text-decoration: none;
                    color: $color-white;
                    font-size: $size-regular;
                    font-weight: $font-weight-medium;
                    padding: 5rem $padding-default;
                    white-space: nowrap;

                    &.active,
                    &:hover {
                        background-color: $color-dark-purple;
                    }
                }
            }
        }
    }
}

// IMPORTANT NEEDED TO OVERWRITE EXISITING INLINE STYLING - Jquery UI Form
%popup--forced {
    background-color: $color-grey-bg-40;
    width: ($base-size * 20)!important;
    max-width: 100%!important;
    text-align: left;
    position: fixed!important;
    top: 50%!important;
    transform: translateY(-50%);
    left: 0!important;
    right: 0!important;
    margin: auto;
    z-index: 101;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    height: auto!important;
    max-height: 100%;
    max-height: 100vh;
    @include media-only-mobile {
        width: 100%!important;
        margin-left: 0;
        height: 100%!important;
    }
}