@function getBorderRadius($type){
    $output: 4px;
    @if $type == 1 {
        $output: 8px;
    }
    @if $type == 2 {
        $output: 16px;
    }
    @if $type == 3 {
        $output: 24px;
    }
    @if $type == 4 {
        $output: 32px;
    }
    @if $type == "other" {
        $output: 40px;
    }
    @return $output;
}

@mixin border-radius($desktop: 0, $mobile: -1) {
    @if $mobile == -1 {
        border-radius: getBorderRadius($desktop);
    } @else {
        @include media-to-desktop {
            border-radius: getBorderRadius($mobile);
        }
        @include media-from-desktop {
            border-radius: getBorderRadius($desktop);
        }
    }
}

@mixin elevator-none-image($border-radius-val: 3) {
    @include border-radius($border-radius-val);
    &::after {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        content: ' ';
        pointer-events: none;
        @include elevator;
        @include transition(all, 0.5s);
        @include border-radius($border-radius-val);
    }

    &:hover {
        @include media-from-desktop {
            &::after {
                @include elevator("hover");
                transform: scaleY(1.1);    
            }   
        }
    }
}

@mixin elevator($state: "default", $vertical-shadow-length: 4px, $blur-radius: 16px, $rgba: rgba(43, 20, 77, 0.16), $rgba-hover: rgba(43, 20, 77, 0.4), $blur-hover-radius: 16px){
    @if $state == "none" {
        box-shadow: 0 0 0 transparent;
    }

    @if $state == "default" {
        box-shadow: 0px $vertical-shadow-length $blur-radius 0px $rgba;
    }

    @if $state == "hover" {      
        box-shadow: 0px $vertical-shadow-length $blur-hover-radius 0px $rgba-hover;
    }
}

@mixin image-radius($scale: 1) {
    $scale: $scale * 0.5;
    border-radius: $base-size*$scale $base-size*$scale $base-size*$scale $base-size*$scale*5;
}

@mixin image-radius-by-corner($scale: 1, $big-scale: 5, $corner-scale: "bottom-right") {
    $scale: $scale * 0.5;
    @if $corner-scale == "bottom-right" {
        border-radius: $base-size*$scale $base-size*$scale $base-size*$scale*$big-scale $base-size*$scale;
    } @else if $corner-scale == "bottom-left" {
        border-radius: $base-size*$scale $base-size*$scale $base-size*$scale $base-size*$scale*$big-scale;
    } @else if $corner-scale == "top-left" {
        border-radius: $base-size*$scale*$big-scale $base-size*$scale $base-size*$scale $base-size*$scale;
    } @else if $corner-scale == "top-right" {
        border-radius: $base-size*$scale $base-size*$scale*$big-scale $base-size*$scale $base-size*$scale;
    } @else {
        border-radius: $base-size*$scale $base-size*$scale $base-size*$scale $base-size*$scale*$big-scale;
    }      
}


@mixin scale-image($isTranslate: true) {
    @if $isTranslate == true {
        -webkit-transform: scale(1.11) translateY(-5%);
                transform: scale(1.11) translateY(-5%);
    } @else {
        -webkit-transform: scale(1.11);
                transform: scale(1.11);
    }
}

@mixin custom-hero-banner-title {
    // TODO: should be moved to mixin later
    .gt-hero-title {
        max-width: $banner-title-size;
        @include media-from-lgtablet {
            max-width: $hero-banner-title-max-width;
        }
        @media (min-width: $screen-lgtab) and (max-width: $screen-lg) {
            font-size: $size-default*6;
            line-height: 6.6rem;
            @include gt-font-weight("black", "gt-hero-title");
        }
    }

    .gt-main-title {
        max-width: $banner-title-size;
    }
}

@mixin new-typo-select-box() {
    .search-results__sort-select {
        position: relative;
        float: right;
        margin-bottom: $margin-default/4;
        width: auto;
        background-color: $color-white;
        @include border-radius(4);
    
        @include media-from-lgtablet {
            margin-left: auto;
            margin-bottom: 0;
        }
    
        &::after {
            display: none;
        }
    
        .select-text {
            z-index: 1;
            font-size: $size-tiny;
            line-height: $size-xxxx-large;
            background: transparent;
            color: $color-black;
        }
    
        select {
            position: relative;
            z-index: 3;
            font-size: $size-tiny;
            line-height: 1.8rem;
            background-color: transparent;
            @include border-radius(4);
            border-color: transparent;
            &:focus {
                border-width: 0.2rem;
                border-color: var(--color-focus-visible, $color-teal);
            }
        }
    
        &-arrow {
            position: absolute;
            top: 50%;
            right: 1.6rem;
            z-index: 2;
            transform: translate(0, -50%);
            line-height: 0;
            border-radius: 100%;
            background: $color-core-purple;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $color-white;
            @include transition(transform, 0.5s);
        }
        .styleSelBox {
            height: auto;
        }
    }
}

@mixin popup-v2 {
    position: relative;
    left: 0;
    margin-top: $margin-default;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100% - #{$base-size});
    padding: $padding-default $padding-default*0.375;
    border-radius: $base-size*3/4 $base-size*3/4 0 0;
    @include media-from-tablet {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        margin: 0;
        max-width: calc(100% - #{$base-size*3});
        height: fit-content;
        max-height: calc(100% - #{$base-size*2});
        @include border-radius(4);
    }
    @include media-from-desktop {
        padding-left: $padding-default*0.625;
        padding-right: $padding-default*0.625;
    }
    @include media-from-desktop-md {
        padding-top: $padding-default*2.3125;
        padding-bottom: $padding-default*1.40625;
        width: fit-content;
        max-width: 117rem;
        max-height: calc(100% - #{$base-size*3});
    }
    .popup-inner {
        height: 100%;
        overflow: auto;
        padding-left: $padding-default*0.375;
        padding-right: $padding-default*0.375;
        @include webkit-scrollbar(0.3rem, $color-light-purple, $color-light-grey, false);
        @include media-from-desktop {
            padding-left: $padding-default*0.625;
            padding-right: $padding-default*0.625;
        }
    }
    .popup-close-btn {
        @include btn-close;
        position: absolute;
        top: #{-$margin-default*0.5};
        right: $margin-default*0.75;
        z-index: 2;
        @include media-from-tablet {
            top: #{-$margin-default*0.75};
        }
        @include media-from-desktop-md {
            right: $margin-default;
        }
    }
    .EPiServerForms {
        .Form__Title {
            @include gt-subtitle(true, bold);
        }
    }
}

@mixin information-card($theme: "white") {
    @if $theme == "purple" {
        --theme-background-color: #{$color-dark-purple};
        --theme-text-overline-color: #{$color-white};
        --theme-text-title-color: #{$color-white};
        --theme-text-button-color: #{$color-light-purple};
    }
    padding: $padding-default*1.25 $padding-default;
    background-color: var(--theme-background-color, $color-white);
    @include elevator;
    @include border-radius(3);
    @include transition;
    @include hover {
        @include elevator("hover");
    }
    .card-overline {
        @include gt-body-text(small, regular);
        text-transform: uppercase;
        color: var(--theme-text-overline-color, $color-dark-theme);
        + .card-title {
            margin-top: $margin-default*0.25;
        }
    }
    .card-title {
        @include gt-small-title(false, regular);
        color: var(--theme-text-title-color, $color-core-purple);
    }
    .btn-secondary {
        margin-top: $margin-default*0.5;
        .svg-animation-vi {
            margin-left: -1rem;
            color: var(--theme-text-button-color, $color-dark-coral);
        }
    }
}